export class StateField {
  constructor(initData, networkGetter=()=>{}, networkSetter=()=>{}, resolver=()=>{}) {
    this.data = initData;

    this._get = async () => {
      this.data = await networkGetter();
      resolver({...this});
    };

    this.set = async (data) => {
      await networkSetter(Object.assign(this.data, data), this.data.id);
      await this._get();
    };
  }

  static default = {
    data: {},
    _get: () => {},
    set: () => {},
  };
}

export class ListField {
  constructor({
    initData,
    networkListGetter=()=>{},
    networkItemCreator=()=>{},
    networkItemUpdater=()=>{},
    networkItemRemover=()=>{},
    networkItemGetterById=()=>{},
    networkItemGetterByKeywords=()=>{},
    resolver=()=>{}
  }) {
    this.data = initData;

    this._getList = async () => {
      this.data = await networkListGetter();
      resolver({...this});
    };

    this.getItemById = async (id) => {
      return await networkItemGetterById(id);
    };

    this.getItemByKeywords = async (keywords) => {
      return await networkItemGetterByKeywords(keywords);
    };

    this.create = async (data) => {
      await networkItemCreator(data);
      await this._getList();
    };

    this.update = async (data) => {
      await networkItemUpdater(data);
      await this._getList();
    };

    this.remove = async (id) => {
      await networkItemRemover(id);
      await this._getList();
    };
  }

  static default = {
    data: {},
    _getList: () => {},
    getItemById: () => {},
    getItemByKeywords: () => {},
    create: () => {},
    update: () => {},
    remove: () => {},
  };
}
