import React, {useContext, useState} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import DataContext from "../../../../DATA_CONTROL/DataContext";
import CustomersB2B from "../Customers/CustomersB2B";
import Button from "../../../common/core/Button";
import CustomersB2P from "../Customers/CustomersB2P";
import ElectByIndex from "../../../common/core/ElectByIndex";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import AddNewCompetitor from './AddNewCompetitor';
import styles from "../Customers/style.module.css";

// const AddNewMarket = () => null;

const columns = [{
  Header: 'CODE#',
  accessor: 'id',
  width: 65,
  index: 0,
},{
  Header: 'NAME',
  accessor: 'name',
  width: 100,
  index: 1,
},{
  Header: 'ADDRESS',
  accessor: 'address',
  width: 120,
  index: 2,
},{
  Header: 'EMAIL',
  accessor: 'email',
  width: 150,
  index: 3,
},{
  Header: 'PHONE',
  accessor: 'phone',
  width: 100,
  index: 4,
},{
  Header: 'WHAT DOES THEIR COMPANY DO?',
  accessor: 'specialization',
  minWidth: 140,
  index: 5,
},{
  Header: 'WHO ARE THEIR CUSTOMERS',
  accessor: 'customers',
  minWidth: 130,
  index: 7,
},{
  Header: 'BUSINESS FACEBOOK PAGE LINK',
  accessor: 'facebook',
  minWidth: 160,
  index: 8,
},{
  Header: 'KEYWORDS',
  accessor: 'keywords',
  width: 100,
  index: 9,
}];

function Competitors() {
  const dataContext = useContext(DataContext);
  const [modalIndex, setModalIndex] = useState(null);

  const addCompetitor = (formState) => {
    dataContext.competitors.add(formState);
    closeModal();
  };

  const closeModal = () => setModalIndex(null);


  const table = <EditableTable
    data={dataContext.competitors.data}
    deleteItem={dataContext.competitors.delete}
    editItem={dataContext.competitors.edit}
    columns={columns}
    headerClassName={styles.tableHeader}
  />;

  const TABS = [{
    name: 'COMPETITORS',
    content: table,
    controllers: [
      <Button
        title={'add new'}
        onClick={()=>setModalIndex(0)}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }];

  const MODALS = [
    <AddNewCompetitor
      onSubmit={addCompetitor}
      close={closeModal}
    />,
  ];

  const content = TABS.map(item => item.content);

  const main = <DashboardTabs
    tabs={TABS}
    readyContents={content}
    startTabIndex={0}
  />;

  return (
    <ElectByIndex
      index={modalIndex}
      defaultComponent={main}
    >
      {MODALS}
    </ElectByIndex>
  );
}


export default Competitors;
