import React, {useState, useContext, useEffect} from 'react';
import styles from './style.module.css';
import {Form} from 'informed';
import ButtonsPair from '../../common/parts/ButtonsPair';
import Profile from './Profile';
import Company from '../../SignUp/contents/Company';
import Business from '../../SignUp/contents/Business';
import DashboardTabs from "../../common/parts/DashboardTabs";
import Enum from "../../../utils/enum";
import {AccountDataContext, BusinessDataContext, CompanyDataContext} from "../../../DATA_CONTROL/dataContexts";
import VirtualAssistant from "./VirtualAssistant";
import {deepClone} from "../../../utils/StringUtils";

const [ACCOUNT, COMPANY, BUSINESS] = Enum(3);

export default function EditUserInfo({close, showing}) {
  const account = useContext(AccountDataContext);
  const company = useContext(CompanyDataContext);
  const business = useContext(BusinessDataContext);

  const [formApis, setFormApis] = useState(Array(4));
  const setFormApi = (index, state) => {
    setFormApis(({[index]: oldState, ...arr}) => ({[index]: state, ...arr}));
  };

  const setFormData = (index, data) => {
    if (formApis[index]) {
      formApis[index].reset();
      if (data.gender !== undefined) data.gender=String(data.gender);
      formApis[index].setValues({...data});
    }
  };

  const setAccountFormData = () => setFormData(ACCOUNT, account.data);
  const setCompanyFormData = () => setFormData(COMPANY, company.data);
  const setBusinessFormData = () => setFormData(BUSINESS, business.data);

  useEffect(setAccountFormData, [formApis, account]);
  useEffect(setCompanyFormData, [formApis, company]);
  useEffect(setBusinessFormData, [formApis, business]);

  useEffect(() => {
    setAccountFormData();
    setCompanyFormData();
    setBusinessFormData();
  }, [showing]);

  const TABS = [{
      name: 'PROFILE',
      content: <Profile/>,
    },{
      name: 'COMPANY',
      content: <Company noHeader/>,
    },{
      name: 'BUSINESS',
      content: <Business grey noHeader/>,
    },{
      name: 'VIRTUAL ASSISTANT',
      content: <VirtualAssistant/>,
  }];

  const onFormSubmit = (tabIndex, formState) => {
    const data = deepClone(formState);
    if (data.gender !== undefined) data.gender=Number(data.gender);
    switch (tabIndex) {
      case ACCOUNT:
        account.set(data);
        break;
      case COMPANY:
        company.set(data);
        break;
      case BUSINESS:
        business.set(data);
        break;
      default:
        break;
    }
  };

  const forms = TABS.map((tab, index) => (
    <div key={tab.name} className={styles.formContainer}>
      <Form
        className={styles.form}
        onSubmit={(formState) => onFormSubmit(index, formState)}
        getApi={(api) => setFormApi(index, api)}
      >
        {tab.content}
        <ButtonsPair onCancel={close}/>
      </Form>
    </div>
  ));

  return (
      <div style={{position: 'absolute'}}>
        <DashboardTabs
            tabs={TABS}
            startTabIndex={0}
            readyContents={forms}
        />
      </div>
  );
}
