import React, {useState} from 'react';
import styles from './style.module.css';
import cn from 'classnames';
import Input from "../../../common/core/Input";
import Col from "react-bootstrap/Col";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import Row from "react-bootstrap/Row";
import ButtonsPair from "../../../common/parts/ButtonsPair";
import useWaiting from "../../../common/hooks/useWaiting";
import AutoForm from "../../../common/core/AutoForm";
import {isEmpty} from "../../../../utils/StringUtils";

const CustomInput = ({title, ...rest}) => (
  <>
    <Col className={cn(styles.title, 'col-12 pl-2 mb-3')}>{title.toUpperCase()}</Col>
    <Col className={'col-12'}>
      <Input heigth={40} {...rest}/>
    </Col>
  </>
);

const CustomForm = ({onSubmit, close, initialValues}) => {
  const [waiting, submit] = useWaiting(onSubmit);
  return (
    <AutoForm
      onSubmit={submit}
      className={styles.form}
      initialValues={initialValues}
    >
      <Row>
        <Col className={'col-4 pr-2'}>
          <CustomInput
            field={'name'}
            title={'name of customer'}
          />
        </Col>
        <Col className={'col-6 pl-2'}>
          <CustomInput
            // field={'address'}
            field={'fulladdress'}
            title={'address'}
          />
        </Col>
      </Row>
      <Row className={'pt-2'}>
        <Col className={'col-4 pr-2'}>
          <CustomInput
            field={'phone'}
            title={'phone'}
          />
        </Col>
        <Col className={'col-6 pl-2'}>
          <CustomInput
            field={'email'}
            title={'email'}
          />
        </Col>
      </Row>
      <Row>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'directionactivity'}
            title={'what does their company do?'}
          />
        </Col>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'needs'}
            title={'what does their company need?'}
          />
        </Col>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'responsibleforpurchasing'}
            title={'person who decide'}
          />
        </Col>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'buyedlasttime'}
            title={'what did they took last time?'}
          />
        </Col>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'keywords'}
            title={'keywords'}
            placeholder={'Enter comma-separated keywords'}
          />
        </Col>
      </Row>
      <Row>
        <ButtonsPair
          saveWaiting={waiting}
          onCancel={close}
          className={'pb-2'}
        />
      </Row>
    </AutoForm>
  );
};

const ModalB2B = ({
  onSubmit,
  close,
  editingValues
}) => {
  const TABS = [{
    name: isEmpty(editingValues) ? 'ADD NEW B2B CUSTOMER' : `EDIT B2B CUSTOMER #${editingValues.id}`,
    content: (
      <CustomForm
        initialValues={editingValues}
        onSubmit={onSubmit}
        close={close}
      />
    ),
  }];

  return (
    <DashboardTabs
      tabs={TABS}
      readyContents={TABS.map(tab => tab.content)}
    />
  );
};

export default ModalB2B;
