import useMouseDown from "../../hooks/useMouseDown";
import styles from './style.module.css';
import cn from 'classnames';
import React, {useRef} from "react";

const TouchableOpacity =  React.forwardRef(({children, className, underlyingColor='transparent', ...otherProps}, ref) => {
  //
  ref = ref ? ref : useRef();
  const pressing = useMouseDown(ref);

  return (
    <div
      className={cn(styles.element, pressing && styles.pressing, className)}
      ref={ref}
      {...otherProps}
    >
      {children}
    </div>
  );
});

export default TouchableOpacity;