import {exists} from "../utils/StringUtils";

export class AccountModel {

  static default = {
    "name": "Name",
    "surname": "Surname",
  };

  constructor({
    login,
    name='Name',
    surname='Surname',
    password,
    dateofbirth='2000-01-01T00:00:00.000Z',
    workemail,
    alternativermail=undefined,
    gender=0,
    facebook=undefined,
  }) {
    this.login = String()
  }
}

export class BusinessModel {
  static default = {
    "id": 0,
    "businessmodel": "",
    "experience": "",
    "yearaverage": "",
    "deliverymodel": "",
    "deliverylocations": "",
    "businessmission": ""
  };

  constructor({id, businessmodel, experience, yearaverage, deliverymodel, deliverylocations, businessmission}) {
    this.id = exists(id) ? Number(id) : BusinessModel.default.id;
    this.businessmodel = exists(businessmodel) ? String(businessmodel) : BusinessModel.default.businessmodel;
    this.experience = exists(experience) ? String(experience) : BusinessModel.default.experience;
    this.yearaverage = exists(yearaverage) ? String(yearaverage) : BusinessModel.default.yearaverage;
    this.deliverymodel = exists(deliverymodel) ? String(deliverymodel) : BusinessModel.default.deliverymodel;
    this.deliverylocations = exists(deliverylocations) ? String(deliverylocations) : BusinessModel.default.deliverylocations;
    this.businessmission = exists(businessmission) ? String(businessmission) : BusinessModel.default.businessmission;
  }
}

