import {Form} from "informed";
import styles from "./style.module.css";
import Input from "./Input";
import loginImagePath from "./images/login.png";
import passwordImagePath from "./images/password.png";
import Button from "../common/core/Button";
import React, {useEffect} from "react";
import InputPlaceholder from "./InputPlaceholder";
import Row from "react-bootstrap/Row";
import {withRouter} from "react-router-dom";
import cn from 'classnames';

const SignUpForm = ({onSubmit, failed, waiting, failText}) => (
  <Form onSubmit={onSubmit} className={styles.form}>
    <Input
      field={"login"}
      placeholder={<InputPlaceholder imageSrc={loginImagePath} text={"Login"}/>}
      required
    />

    <Input
      field={"password"}
      type={"password"}
      placeholder={<InputPlaceholder imageSrc={passwordImagePath} text={"Password"}/>}
      required
    />

    <Input
      field={"workemail"}
      placeholder={<InputPlaceholder text={"Work email"}/>}
      required
    />

    <Input
      field={"companyname"}
      placeholder={<InputPlaceholder text={"Company name"}/>}
    />

    <div className={cn(styles.fail, failed && styles.failActive)}>
      {failed && failText}
    </div>

    <Row className={styles.buttonContainer}>
      <Button waiting={waiting} containerClassName={styles.button} title={'Register'} type={"submit"}/>
    </Row>

  </Form>
);

export default withRouter(({history, success, ...props}) => {
  useEffect(() => {
    if (success) history.push('/register');
  }, [success]);

  return (
    <SignUpForm {...props}/>
  );
});
