import React, {useState} from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Input, {GenderSelect, SelectInput} from '../../../common/core/Input';
import styles from './style.module.css';
import Image from "react-bootstrap/Image";
import cn from 'classnames';
import TouchableOpacity from "../../../common/core/TouchableOpacity";

//dev
const IMAGE = require('../../images/robot.jpg');

const VirtualAssistant = () => {
  const [selectedIndex, setSelectedIndex] = useState(0);

  // get it from context
  const avatarImageSources = [...Array(6)].fill(IMAGE);

  const avatarImages = avatarImageSources.map((imageSrc, index) => (
    <Avatar
      imageSrc={imageSrc}
      selected={index===selectedIndex}
      onClick={() => setSelectedIndex(index)}
    />
  ));


  return (
    <>
      <Section
        left={'Choose a name for your Virtual Assistant'}
        right={
          <Input
            containerClassName={styles.sectionInputContainer}
            field={'assistantname'}
            required
          />
        }
      />
      <Section
        left={'Choose a voice you’d  like for him/her'}
        right={
          <GenderSelect/>
        }
      />
      <Section
        left={'Choose an avatar for your Virtual Assistant'}
        right={
          <Row
            className={styles.avatarsHolder}
          >
            {avatarImages}
          </Row>
        }
      />
    </>
  );
};

const Section = ({left, right}) => (
  <Row className={styles.sectionContainer}>
    <Col
      className={styles.leftContainer}
      xs={4}
    >
      {left}
    </Col>
    <Col
      className={styles.rightContainer}
      xs={8}
    >
      {right}
    </Col>
  </Row>
);

const AVATAR_HEIGHT = 150;

const Avatar = ({imageSrc, selected, onClick}) => (
  <Col
    xs={4}
    className={styles.avatarCol}
  >
    <TouchableOpacity
      style={{
        height: AVATAR_HEIGHT,
      }}
      className={cn(
        styles.avatarContainer,
        selected && styles.avatarContainerSelected
      )}
      onClick={onClick}
    >
      <Image
        fluid
        src={imageSrc}
        className={styles.avatarImage}
      />
    </TouchableOpacity>
  </Col>
);

export default VirtualAssistant;
