import React from 'react';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Input from "../../common/core/Input";
import styles from '../style.module.css';
import {TextArea, Scope} from "informed";
import moment from "moment";

// export const Input = (props) => <ImportedInput {...props}/>;

// passing bool 'noHeader' to easy style it in UserSettings.
export default ({noHeader}) => (
  <Row>

    {!noHeader &&
    <Row className={styles.subheader + ' mb-3'}>
      Your company description and contacts
    </Row>}

    <Row>

      <Col className={"col-6 m-0 pr-2 text-center"}>

        <Input field={'name'} placeholder={'Company name'} required />
        <Input field={'email'} type={'email'} placeholder={'Email'} required />
        <Input field={'phone'} placeholder={'Phone'} required/>


        <Scope scope={'address'}>
          <Row>
            <Col className={"col-8 pr-2"}>
              <Input field={'street'} placeholder={'Street'} required />
              <Input field={'city'} placeholder={'City'} required />
              <Input field={'country'} placeholder={'Country'} required />
            </Col>
            <Col className={"col-4 pl-2"}>
              <Input field={'building'} placeholder={'№'} required />
              <Input field={'index'} placeholder={'Index'} required />
              <Input field={'state'} placeholder={'State'} required />
            </Col>
          </Row>
        </Scope>

      </Col>

      <Col className={"col-6 m-0 pl-2 text-center"}>
        <Input field={'registrationnumber'} placeholder={'Company registration number'}/>
        <Input
            field={'dateofestablishment'}
            parse={(value) => moment(value).format("YYYY-MM-DDTHH:mm:ss.SSSZ")}
            format={(value) => moment(value).format("YYYY-MM-DD")}
            placeholder={'Date of establishment'} type={"date"} max={'3000-01-01'}
        />
        <Input field={'licencetype'} placeholder={'Type of license, issued'}/>
        <Row>
          <Col className={"col-6 pr-2"}>
            <Input field={'certification'} placeholder={'Certification'}/>
          </Col>
          <Col className={"col-6 pl-2"}>
            <Input field={'authorizations'} placeholder={'Authorizations'}/>
          </Col>
        </Row>
        <Input field={'qualitycertificates'} placeholder={'Quality certificates, issued'}/>
        <Input field={'brandscertificates'} placeholder={'Brand certificates and copyrights, issued'}/>
      </Col>

    </Row>

    {/*<Col className={'col-12'}>*/}
    {/*  <TextArea required field={'15'} placeholder={'Banking details'} className={cn(styles.areaCompany, styles.banking)}/>*/}
    {/*</Col>*/}
    <Col className={'col-12'}>
      <TextArea required field={'description'} placeholder={'Text field max 3 propositions about founders and type of company'} className={styles.areaCompany}/>
    </Col>

  </Row>
);

