import React, {useRef, useState, useEffect, useContext} from "react";
import ReactQuill from 'react-quill';
import './quill.snow.css';
import styles from './style.module.css';
import './quillStyles.css';
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import Button from "../../../../common/core/Button";
import Col from "react-bootstrap/Col";
import {Text, Form} from 'informed';
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import cn from 'classnames';
import {VariableFieldsDataContext} from "../../../../../DATA_CONTROL/dataContexts";

const modules = {
  toolbar: [
    [{ 'header': [1, 2, false] }],
    ['bold', 'italic', 'underline'],
    [{'list': 'ordered'}, {'list': 'bullet'}],
    [{'align': []}],
    [{container: '#'}]
  ],
};

const Editor = ({onSave, onClose, defaultHtmlText, withFields=true, type='template'}) => {
  const fields = useContext(VariableFieldsDataContext);

  const editor = useRef();
  const [content, setContent] = useState(defaultHtmlText);
  useEffect(() => {
    setContent(defaultHtmlText);
  }, [defaultHtmlText]);
  const [formApi, setFormApi] = useState(null);
  const resetFailsFunc = useRef(()=>{});
  const setResetFailsFunc = (f) => {
    resetFailsFunc.current = f;
  };

  const onDragOver = (e) => {
    e.dataTransfer.dropEffect = 'copy';
    e.dataTransfer.effectAllowed = 'all';
  };

  useEffect(() => {
    if (!editor) return;
    const controller = editor.current;
    const area = controller.editor.root;
    area.addEventListener('dragover', onDragOver);

    return () => {
      area.removeEventListener('dragover', onDragOver);
    }
  });

  const [waiting, setWaiting] = useState(false);
  const save = async ({title, keywords}) => {
    setWaiting(true);
    await onSave({title, keywords, content});
    setWaiting(false);
    close();
  };

  const close = () => {
    setContent(defaultHtmlText);
    resetFailsFunc.current();
    formApi.reset();
    onClose();
  };

  const fieldComponents = fields.map(({fieldName, description}) => (
    <Field text={fieldName} hint={description}/>
  ));

  return (
    <>
      <div className={styles.main}>
        <div className={styles.mainContent}>
          <CustomForm
            getApi={setFormApi}
            onSubmit={save}
            close={close}
            setResetFailsFunc={setResetFailsFunc}
            type={type}
          />
          <div className={styles.editor}>
            <Buttons save={formApi && formApi.submitForm} cancel={close} waiting={waiting}/>
            <ReactQuill
              className={'tmpl'}
              value={content}
              onChange={(text) => setContent(text)}
              modules={modules}
              ref={editor}
            />
          </div>
        </div>
      </div>
      {withFields && (
        <div className={styles.fields}>
          <div className={styles.fieldsContent}>
            <div className={styles.header}>
              ADD VARIABLE FIELDS:
            </div>
            {fieldComponents}
          </div>
        </div>
      )}
    </>
  );
};

const CustomForm = ({onSubmit, setResetFailsFunc, close, type, ...props}) => {
  const validate = value => !value || undefined;
  const [inputFails, setInputFails] = useState({});

  const submit = (formState) => {
    // TODO async so what?!!
    setFails({});
    onSubmit(formState);
  };

  const setFails = (states) => {
    setInputFails({...states});
  };

  useEffect(() => {
    if (setResetFailsFunc) {
      const resetFailsFunc = () => setFails({});
      setResetFailsFunc(resetFailsFunc);
    }
  }, [setResetFailsFunc]);

  return (
    <Container className={styles.formContainer}>
      <Form
        onSubmitFailure={setFails}
        onSubmit={submit}
        autoComplete="off"
        initialValues={{title: type === 'template' ? 'Untitled template' : 'Untitled document'}}
        {...props}
      >
        <Row>
          <Col className={cn(styles.inputContainer, styles.titleInputContainer, inputFails.title && styles.error)} xs={12}>
            <Text
              placeholder={'Enter title for your template'}
              field={'title'}
              validate={validate}
            />
            <TouchableOpacity
              className={styles.backButtonContainer}
              onClick={close}
            >
              <i className="fas fa-caret-left"/>
            </TouchableOpacity>
          </Col>
          <Col className={cn(styles.inputContainer, inputFails.keywords && styles.error)} xs={12}>
            <Text
              placeholder={'Enter comma-separated keywords'}
              field={'keywords'}
              validate={validate}
            />
          </Col>
        </Row>
      </Form>
    </Container>
  );
};

const Buttons = ({save, cancel, waiting}) => (
  <div className={styles.buttonsContainer}>
    <Button
      title={'cancel'}
      onClick={cancel}
      fontSize={'0.9rem'}
      height={24}
      width={'unset'}
      color={'#fff'}
      textColor={'#C9CBCA'}
    />
    <Button
      title={'save'}
      fontSize={'0.9rem'}
      height={24}
      onClick={save}
      width={60}
      loaderSize={5}
      color={'#fff'}
      textColor={'#1596C0'}
      containerClassName={'mr-2'}
      waiting={waiting}
    />
  </div>
);

const Field = ({text, hint}) => {
  const field = useRef();

  const onDragStart = (e) => {
    window.getSelection().removeAllRanges();
    const text =  e.target.querySelector(`.${styles.text}`).textContent.toUpperCase();
    e.dataTransfer.setData('text/plain', text.split(' ').join('_'));
  };

  useEffect(() => {
    if (!field) return;
    const el = field.current;

    el.addEventListener('dragstart', onDragStart);

    return () => {
      el.removeEventListener('dragstart', onDragStart);
    }
  });

  return (
    <TouchableOpacity draggable ref={field} className={styles.field}>
      <div className={styles.text}>[{text}]</div>
      {hint && <div className={styles.hint}>{hint}</div>}
    </TouchableOpacity>
  );
};


export default Editor;
