import React, {useRef} from 'react';
import styles from './style.module.css';
import cn from 'classnames';
import Input, {DateInput} from "../../../common/core/Input";
import Col from "react-bootstrap/Col";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import Row from "react-bootstrap/Row";
import ButtonsPair from "../../../common/parts/ButtonsPair";
import useWaiting from "../../../common/hooks/useWaiting";
import moment from "moment";
import AutoForm from "../../../common/core/AutoForm";
import {isEmpty} from "../../../../utils/StringUtils";

const CustomInput = ({title, InputComponent=Input, ...rest}) => (
  <>
    <Col className={cn(styles.title, 'col-12 pl-2 mb-3')}>{title.toUpperCase()}</Col>
    <Col className={'col-12'}>
      <InputComponent heigth={40} {...rest}/>
    </Col>
  </>
);

const CustomForm = ({onSubmit, close, initialValues={}}) => {
  const [waiting, submit] = useWaiting(onSubmit);
  const defaultValues = {dateofbirth: moment()};
  return (
    <AutoForm
      onSubmit={submit}
      className={styles.form}
      initialValues={isEmpty(initialValues) ? defaultValues : initialValues}
    >
      <Row>
        <Col className={'col-4 pr-2'}>
          <CustomInput
            field={'name'}
            title={'name of customer'}
          />
        </Col>
        <Col className={'col-6 pl-2'}>
          <CustomInput
            // field={'address'}
            field={'fulladdress'}
            title={'address'}
          />
        </Col>
      </Row>
      <Row className={'pt-2'}>
        <Col className={'col-4 pr-2'}>
          <CustomInput
            field={'phone'}
            title={'phone'}
          />
        </Col>
        <Col className={'col-6 pl-2'}>
          <CustomInput
            field={'email'}
            title={'email'}
          />
        </Col>
        <Col className={'col-4 pt-2 pr-2'}>
          <CustomInput
            InputComponent={DateInput}
            field={'dateofbirth'}
            title={'date of birth'}
          />
        </Col>
        <Col className={'col-6 pt-2 pl-2'}>
          <CustomInput
            field={'facebookprofile'}
            title={'facebook profile'}
          />
        </Col>
      </Row>
      <Row>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'directionactivity'}
            title={'what does he/she do?'}
          />
        </Col>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'buyedlasttime'}
            title={'what does he/she bought last time?'}
          />
        </Col>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'keywords'}
            title={'keywords'}
            placeholder={'Enter comma-separated keywords'}
          />
        </Col>
      </Row>
      <Row>
        <ButtonsPair
          saveWaiting={waiting}
          onCancel={close}
          className={'pb-2'}
        />
      </Row>
    </AutoForm>
  );
};

const ModalB2P = ({
  onSubmit,
  close,
  editingValues
}) => {
  const TABS = [{
    name: isEmpty(editingValues) ? 'ADD NEW B2P CUSTOMER' : `EDIT B2P CUSTOMER #${editingValues.id}`,
    content: (
      <CustomForm
        onSubmit={onSubmit}
        close={close}
        initialValues={editingValues}
      />
    ),
  }];

  return (
    <DashboardTabs
      tabs={TABS}
      readyContents={TABS.map(tab => tab.content)}
    />
  );
};

export default ModalB2P;
