import React, {useState} from 'react';
import cn from "classnames";
import styles from "./style.module.css";
import Row from "react-bootstrap/Row";
import ElectByIndex from '../../core/ElectByIndex';
import Col from "react-bootstrap/Col";
import TouchableOpacity from "../../core/TouchableOpacity";

const DashboardTabs = ({
  tabs,
  startTabIndex=0,
  readyContents,
}) => {
  const [activeTabIndex, setActiveTabIndex] = useState(startTabIndex);

  // tabs
  const tabComponents = tabs.map((tab, index) => (
    <Tab
      name={tab.name}
      style={tab.style}
      active={index===activeTabIndex}
      onClick={() => {
        if (tab.onClick) {
          tab.onClick();
        } else {
          setActiveTabIndex(index);
        }
      }}
    />
  ));

  // right-side buttons
  const controllers = <ElectByIndex className={styles.controllers} index={activeTabIndex}>
    {tabs.map(tab => tab.controllers && tab.controllers.map(controller => <Col className={styles.controller}>{controller}</Col>))}
  </ElectByIndex>;

  // main contents
  const contents = <ElectByIndex index={activeTabIndex}>{readyContents}</ElectByIndex>;

  return (
    <div className={styles.container}>
      <Row className={styles.row}>
        <div className={styles.tabs}>{tabComponents}</div>
        {controllers}
      </Row>
        {contents}
    </div>
  );
};

export const Tab = ({name, active=true, onClick=()=>{}, style}) => (
  <TouchableOpacity
    style={style}
    onClick={onClick}
    className={cn(styles.tab, active && styles.active)}
  >
    {name}
  </TouchableOpacity>
);

export default DashboardTabs;
