import React, {useContext} from 'react';
import styles from './style.module.css';
import {Form} from 'informed';
import cn from 'classnames';
import Input from "../../../common/core/Input";
import Col from "react-bootstrap/Col";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import Row from "react-bootstrap/Row";
import ButtonsPair from "../../../common/parts/ButtonsPair";
import {ServicesDataContext} from "../../../../DATA_CONTROL/dataContexts";
import {isEmpty} from "../../../../utils/StringUtils";
import AutoForm from "../../../common/core/AutoForm";
import useWaiting from "../../../common/hooks/useWaiting";

const CustomInput = ({title, ...rest}) => (
  <>
    <Col className={cn(styles.title, 'col-12 pl-2 mb-3')}>{title.toUpperCase()}</Col>
    <Col className={'col-12'}>
      <Input heigth={40} {...rest}/>
    </Col>
  </>
);

const CustomForm = ({onSubmit, close, initialValues}) => {
  const [waiting, submit] = useWaiting(onSubmit);
  return (
    <AutoForm
      onSubmit={submit}
      className={styles.form}
      initialValues={initialValues}
    >
      <Row>
        <Col className={'col-5 pr-2'}>
          <CustomInput
            field={'name'}
            title={'name of service'}
          />
        </Col>
        <Col className={'col-2 px-2'}>
          <CustomInput
            field={'price'}
            title={'price'}
          />
        </Col>
        <Col className={'col-5 pl-2'}>
          <CustomInput
            field={'unitmeasure'}
            title={'u.m. (unit measure)'}
          />
        </Col>
      </Row>
      <Row className={'pt-2'}>
        <Col className={'col-5 pr-2'}>
          <CustomInput
            field={'producer'}
            title={'producer'}
          />
        </Col>
        <Col className={'col-5 pl-2'}>
          <CustomInput
            field={'supply'}
            title={'delivery'}
          />
        </Col>
      </Row>
      <Row>
        <Col className={'col-10 pt-2'}>
          <CustomInput
            field={'generaldescription'}
            title={'general description'}
          />
        </Col>
        <Col className={'col-10'}>
          <CustomInput
            field={'keywords'}
            title={'keywords'}
            placeholder={'Enter comma-separated keywords'}
          />
        </Col>
      </Row>
      <Row>
        <ButtonsPair saveWaiting={waiting} onCancel={close}/>
      </Row>
    </AutoForm>
  );
};

const ModalService = ({
  editingValues,
  close,
}) => {
  const services = useContext(ServicesDataContext);

  const createSubmit = async (formState) => {
    await services.create(formState);
    close();
  };

  const editSubmit = async (formState) => {
    await services.update({...formState, id: editingValues.id});
    close();
  };

  const TABS = [{
    name: isEmpty(editingValues) ? 'ADD NEW SERVICE' : `EDIT SERVICE #${editingValues.id}`,
    content: (
      <CustomForm
        onSubmit={isEmpty(editingValues) ? createSubmit : editSubmit}
        initialValues={editingValues}
        close={close}
      />
    ),
  }];

  return (
    <DashboardTabs
      tabs={TABS}
      readyContents={TABS.map(tab => tab.content)}
    />
  );
};

export default ModalService;
