import Input, {DateInput, GenderSelect, SelectInput} from "../../common/core/Input";
import React from "react";
import cn from 'classnames';
import styles from './style.module.css';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import moment from "moment";
import {Option, Scope, Select} from 'informed';

export default () => (
  <Row>
    <Col className={'col-4'}>
      <Input field={'name'} placeholder={'Name'} required />
      <Input field={'surname'} placeholder={'Surname'} required />
      <DateInput
        field={'dateofbirth'}
        placeholder={'Date of birth'}
        required
      />
    </Col>
    <Col className={cn(styles.profileAddress, 'col-12')}>Address</Col>
    <Col className={'col-12'}>
      <Row>
        <Col className={'col-5 pr-2'}>
          <Row>
            <Scope scope={'address'}>
              <Col className={'col-7 pr-1'}>
                <Input field={'street'} placeholder={'Street'} required/>
                <Input field={'city'} placeholder={'City'} required/>
                <Input field={'country'} placeholder={'Country'} required/>
              </Col>
              <Col className={'col-5 pl-1'}>
                <Input field={'building'} placeholder={'№'} required/>
                <Input field={'index'} placeholder={'Index'} required/>
                <Input field={'state'} placeholder={'State'} required/>
              </Col>
            </Scope>
          </Row>
        </Col>
        <Col className={'col-7 pl-2'}>
          <Row>
            <Col className={'col-6 pr-1'}>
              <Input field={'workemail'} placeholder={'Work email'} required/>
              <Input field={'alternativermail'} placeholder={'Alternative email'}/>
              <GenderSelect/>
            </Col>
            <Col className={'col-6 pl-1'}>
              <Input field={'facebook'} placeholder={'Facebook profile'}/>
              <Input field={'twitter'} placeholder={'Twitter profile'}/>
              <Input field={'linkedin'} placeholder={'Linked-in profile'}/>
            </Col>
          </Row>
        </Col>
      </Row>
    </Col>
  </Row>
);
