import {useEffect, useRef, useState} from "react";

const useRecorder = (onRecordEndCallback, onMicrophoneAccessGranted, onMicrophoneAccessDenied) => {
  const [recorder, setRecorder] = useState(null);

  const handleSuccess = (stream) => {
    const context = new AudioContext();
    const source = context.createMediaStreamSource(stream);
    const recorder = new window.Recorder(source, {
      callback: onRecordEnd,
      numChannels: 1
    });
    setRecorder(recorder);
  };

  const onRecordEnd = (blob) => {
    const reader = new FileReader();
    reader.readAsDataURL(blob);
    reader.onloadend = () => {
      const base64data = reader.result;
      onRecordEndCallback(base64data.split(',')[1]);
    };
  };

  // TODO what if two micros

  useEffect(() => {
    let savedStream = null;

    const requestMicrophoneAccess = () => {
      if (!navigator.mediaDevices) {
        onMicrophoneAccessDenied();
        return;
      }

      navigator.mediaDevices.getUserMedia({ audio: true, video: false })
        .then((stream) => {
          savedStream = stream;
          handleSuccess(stream);
          onMicrophoneAccessGranted();
        })
        .catch((err) => {
          console.log(err);
          onMicrophoneAccessDenied();
        });
    };

    const terminate = () => {
      savedStream && savedStream.getTracks()[0].stop();
      if (navigator.mediaDevices) navigator.mediaDevices.ondevicechange = null;
    };

    if (navigator.mediaDevices) navigator.mediaDevices.ondevicechange = requestMicrophoneAccess;

    requestMicrophoneAccess();

    return () => terminate();

  }, []);

  const timer = useRef();
  const recStart = (seconds) => {
    recorder.record();
    timer.current = setTimeout(() => recForceStop(), seconds*1000);
    console.log('rec start');
  };

  const recForceStop = () => {
    clearTimeout(timer.current);
    recorder.stop();
    recorder.exportWAV();
    recorder.clear();
    console.log('rec end');
  };

  return [recStart, recForceStop];
};

export default useRecorder;
