const API_KEY = 'AIzaSyDCqy6yW6wZc1vwGbVzebJFEKijhBadL0o';

const headers = new Headers();
headers.append('Content-Type', 'application/json');

const target = `https://speech.googleapis.com/v1/speech:recognize?key=${API_KEY}`;

const getGoogleSpeechTranscript = async (base64data) => {
  console.log(base64data);

  const body = {
    "config": {
      "encoding": "LINEAR16",
      "languageCode": "en-US"
    },
    "audio": {
      "content": base64data
    }
  };

  const params = {
    method: 'POST',
    headers,
    body: JSON.stringify(body),
  };

  const res = await fetch(target, params);
  const json = await res.json();
  return !!Object.keys(json).length && json.results[0].alternatives[0].transcript;
};

export default getGoogleSpeechTranscript;