import React, {useState, useContext} from "react";
import styles from './style.module.css';
import ElectById from "../../../common/core/ElectById";
import Main from './Main';
import Editor from "./Editor";
import {TemplatesDataContext} from "../../../../DATA_CONTROL/dataContexts";

const itemShape = {date: '20.06.20', title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'};
// TODO extract!
// DEV
const templates = [...Array(7)].fill(itemShape);
const documents = templates.slice(3);

// AUTOSAVE TIMER HERE?

function Templates() {
  const templates = useContext(TemplatesDataContext);

  const [activeElementId, setActiveElementId] = useState('main');

  const openMain = () => setActiveElementId('main');
  const openEditor = () => setActiveElementId('editor');

  // TODO refactor to have ability to edit existing templates
  const saveEditor = async (templateData) => {
    await templates.create(templateData);
  };

  const closeEditor = () => {
    openMain();
  };

  const ELEMENTS = [{
    id: 'main',
    component: <Main
      openEditor={openEditor}
      documents={documents}
    />,
  },{
    id: 'editor',
    component: <Editor
      onSave={saveEditor}
      onClose={closeEditor}
      defaultHtmlText={''}
    />,
  }];

  return (
    <ElectById
        activeElementId={activeElementId}
        containerClassName={styles.container}
    >
      {ELEMENTS}
    </ElectById>
  );
}

export default Templates;
