import React, {useContext, useState} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import DataContext from "../../../../DATA_CONTROL/DataContext";
import Button from "../../../common/core/Button";
import ElectByIndex from "../../../common/core/ElectByIndex";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import AddNewMarket from './AddNewMarket';

const columns = [{
  Header: 'CODE#',
  accessor: 'id',
  width: 65,
  index: 0,
},{
  Header: 'TARGET MARKET (WHO ARE YOUR CUSTOMERS?)',
  accessor: 'market',
  index: 1,
},{
  Header: 'KEYWORDS',
  accessor: 'keywords',
  width: 150,
  index: 2,
}];

function Market() {
  const dataContext = useContext(DataContext);
  const [modalIndex, setModalIndex] = useState(null);

  const addMarket = (formState) => {
    dataContext.market.add(formState);
    closeModal();
  };

  const closeModal = () => setModalIndex(null);

  const table = <EditableTable
    data={dataContext.market.data}
    deleteItem={dataContext.market.delete}
    editItem={dataContext.market.edit}
    columns={columns}
  />;

  const TABS = [{
    name: 'TARGET MARKET',
    content: table,
    controllers: [
      <Button
        title={'add new'}
        onClick={()=>setModalIndex(0)}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }];

  const MODALS = [
    <AddNewMarket
      onSubmit={addMarket}
      close={closeModal}
    />,
  ];

  const content = TABS.map(item => item.content);

  const main = <DashboardTabs
    tabs={TABS}
    readyContents={content}
    startTabIndex={0}
  />;

  return (
    <ElectByIndex
      index={modalIndex}
      defaultComponent={main}
    >
      {MODALS}
    </ElectByIndex>
  );
}


export default Market;
