import React, {useContext, useState} from 'react';
import styles from "./style.module.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "../../../../common/core/Button";
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import Image from "react-bootstrap/Image";
import clipImagePath from "../../../images/icons/templates/clip_red.png";
import cn from "classnames";
import clipboardBlueImagePath from "../../../images/icons/templates/clipboard_blue.png";
import ElectById from "../../../../common/core/ElectById";
import moment from "moment";
import {Col12} from "../../../../common/core/CustomBootstrap/cols";
import TabContext from "../../../TAB_CONTROL/TabContext";
import {DocumentsDataContext, TemplatesDataContext} from "../../../../../DATA_CONTROL/dataContexts";
import {AssistantContext} from "../../../TAB_CONTROL/AssistantContext";

const Main = ({documents, openEditor}) => {
  const [board, setBoard] = useState('freq');

  const templates = useContext(TemplatesDataContext);

  let allTemplates = [...templates.data];
  let freqTemplates = [...templates.data].reverse();

  // let allTemplates = getSortedByDateTemplates(templates);
  // let freqTemplates = getFrequntlyUsedTemplates(templates, 9);

  const BOARDS = [{
    id: 'freq',
    component: <Board
      items={freqTemplates}
      openEditor={openEditor}
      title={'FREQUENTLY USED'}
      switchBoardText={'SEE ALL'}
      switchBoardAction={() => setBoard('all')}
    />,
  }, {
    id: 'all',
    component: <Board
      items={allTemplates}
      openEditor={openEditor}
      title={'YOUR TEMPLATES'}
      switchBoardText={'FREQUENTLY USED'}
      switchBoardAction={() => setBoard('freq')}
    />,
  }];

  const boards = <ElectById
    activeElementId={board}
    containerClassName={styles.main}
  >{BOARDS}</ElectById>;

  return (
    <>
      {boards}
      <List items={documents}/>
    </>
  );
};

export default Main;

const Board = ({title, items, openEditor, switchBoardText, switchBoardAction}) => (
  <div className={styles.boardContainer}>
    <Row className={styles.boardHeader}>
      <Col className={styles.headerName}>{title}</Col>
      <TouchableOpacity
        className={styles.switch}
        onClick={switchBoardAction}
      >
        {switchBoardText}
      </TouchableOpacity>
      <div className={styles.createNew}>
        <Button
          onClick={openEditor}
          thin
          className={styles.createNewButton}
          title={'create new'}
          height={28}
          width={130}/>
      </div>
    </Row>
    <Row className={styles.board}>
      {
        items.length === 0 ?
          <div className={styles.createYourFirst}>
            You have no templates yet. Create one!
          </div>
          :
          items.map(item => <BoardItem info={item}/>)
      }
    </Row>
  </div>
);

const List = () => {
  const tabContext = useContext(TabContext);
  const documents = useContext(DocumentsDataContext);

  return (
    <div className={styles.latestDocuments}>
      <div className={styles.listContainer}>
        <Row className={styles.listHeader}>
          <Col className={styles.headerName}>LATEST DOCUMENTS</Col>
          <TouchableOpacity
            onClick={() => tabContext.setTabIndex(1)}
            className={styles.switch}
          >SEE ALL</TouchableOpacity>
        </Row>
        <Row>
          {documents.data.length > 0 ?
            documents.data.map((item) => <ListItem info={item}/>)
            : <div className={styles.noDocs}>You have no documents yet</div>
          }
        </Row>
      </div>
    </div>
  );
};

const BOARD_ITEM_ICON_SIZE = 25;

const BoardItem = ({info: template}) => {
  const assistant = useContext(AssistantContext);
  return (
    <Col className={'col-4'}>
      <TouchableOpacity onClick={() => {
        assistant.open(template);
      }}>
        <div className={styles.boardItem}>
          <Col12 className={styles.mainInfo}>
            <Col12 className={styles.date}>{moment().format('MM.DD.YYYY')}</Col12>
            <Col12 className={styles.boardItemText}>
              {template.title}
            </Col12>
          </Col12>
          <Col12 className={styles.keywords}>
            {template.keywords}
          </Col12>
          <div className={styles.boardItemIcon}>
            {/*dev*/}<Image height={BOARD_ITEM_ICON_SIZE} src={clipImagePath}/>
          </div>
        </div>
      </TouchableOpacity>
    </Col>
  );
};

const ListItem = ({info}) => (
  <Col className={'col-12'}>
    <TouchableOpacity>
      <div className={styles.listItem}>
        <Col className={cn(styles.listItemIconContainer, 'col-2')}>
          {/*dev*/}<Image height={25} src={clipboardBlueImagePath}/>
        </Col>
        <Col className={'col-10 pr-3'}>
          <Row className={styles.date}>{moment().format('MM.DD.YYYY')}</Row>
          <Row className={styles.listItemText}>
              {info.title}
          </Row>
        </Col>
      </div>
    </TouchableOpacity>
  </Col>
);
