const API_KEY = 'AIzaSyDCqy6yW6wZc1vwGbVzebJFEKijhBadL0o';

const headers = new Headers();
headers.append('Content-Type', 'application/json');

const target = `https://texttospeech.googleapis.com/v1/text:synthesize?key=${API_KEY}`;

const getGoogleTextToSpeech = async (text) => {
  const body = {
    'input': {
      'text': text,
    },
    'voice':{
      'languageCode':'en-gb',
      'name':'en-GB-Standard-A',
      'ssmlGender':'FEMALE'
    },
    'audioConfig':{
      'audioEncoding':'MP3'
    }
  };

  const params = {
    method: 'POST',
    headers,
    body: JSON.stringify(body),
  };

  const res = await fetch(target, params);
  const json = await res.json();

  return json && json.audioContent;
};

export default getGoogleTextToSpeech;