import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import {ProductsDataContext} from "../../../../DATA_CONTROL/dataContexts";

const columns = [{
  Header: 'CODE#',
  accessor: 'id',
  width: 65,
  index: 0,
},{
  Header: 'NAME',
  accessor: 'name',
  width: 120,
  index: 1,
},{
  Header: 'PRICE',
  accessor: 'price',
  width: 80,
  index: 2,
},{
  Header: 'U.M.',
  accessor: 'unitmeasure',
  width: 80,
  index: 3,
},{
  Header: 'PRODUCER',
  accessor: 'producer',
  width: 120,
  index: 4,
},{
  Header: 'GENERAL DESCRIPTION',
  accessor: 'generaldescription',
  minWidth: 250,
  index: 5,
},{
  Header: 'DELIVERY',
  accessor: 'delivery',
  width: 85,
  index: 6,
},{
  Header: 'KEYWORDS',
  accessor: 'keywords',
  width: 100,
  index: 7,
},];

function ProductsTab ({openEditModal}) {
  const products = useContext(ProductsDataContext);
  return (
    <EditableTable
      data={products.data}
      deleteItem={products.remove}
      editItem={openEditModal}
      columns={columns}
      noWhatText={'products'}
    />
  );
}


export default ProductsTab;
