import React, {createContext, useEffect, useReducer} from 'react';
import Assistant from "../Assistant";
import Enum from "../../../utils/enum";

export const contextShape = {
  open: () => {},
  close: () => {},
  assistant: null,
};

export const AssistantContext = createContext(contextShape);

const [
  OPEN, CLOSE, UPDATE_STATE
] = Enum(2);

function reducer(state, {type, assistant, newState}) {
  switch (type) {
    case OPEN:
      return {...state, assistant};
    case CLOSE:
      return {...state, assistant: null};
    case UPDATE_STATE:
      return newState;
    default:
      return state;
  }
}

export const useAssistantContextValue = () => {
  const [state, dispatch] = useReducer(reducer, contextShape);

  const close = () => {
    dispatch({type: CLOSE});
  };

  const open = (template) => {
    dispatch({
      type: OPEN,
      assistant: (
        <Assistant
          template={template}
          close={close}
        />
      ),
    });
  };

  useEffect(() => {
    dispatch({
      type: UPDATE_STATE,
      newState: {
        ...contextShape,
        open,
        close,
      }
    });
  }, []);

  return state;
};
