import {Form} from "informed";
import styles from "./style.module.css";
import Input from "./Input";
import loginImagePath from "./images/login.png";
import passwordImagePath from "./images/password.png";
import Button from "../common/core/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Checkbox from "../common/core/Checkbox";
import {Link} from "react-router-dom";
import React from "react";
import InputPlaceholder from "./InputPlaceholder";
import cn from 'classnames';

export default ({onSubmit, failed, waiting, failText}) => (
  <Form onSubmit={onSubmit} className={styles.form}>

    <Input
      field={"login"}
      placeholder={<InputPlaceholder imageSrc={loginImagePath} text={"Login"}/>}
      required
    />
    <Input
      field={"password"}
      type={"password"}
      placeholder={<InputPlaceholder imageSrc={passwordImagePath} text={"Password"}/>}
      required
    />

    <Row className={'mb-1'}>
      <Col className={styles.rememberContainer}>
        <Checkbox field={"remember"}/>
        <span className={styles.rememberText}>Remember me</span>
      </Col>
      <Col className={styles.forgotText}>
        <Link to={""}>Forgot Login/Password</Link>
      </Col>
    </Row>

    <div className={cn(styles.fail, failed && styles.failActive)}>
        {failed && failText}
    </div>

    <Row className={styles.buttonContainer}>
      <Button waiting={waiting} containerClassName={styles.button} title={'Login'} type={"submit"}/>
    </Row>

  </Form>
);


