export function getEntitiesByTemplateContent(content) {
  const fields = [];
  let temp = '', readingField = false;
  for (let i = 0; i < content.length; i++) {
    if (readingField) temp += content[i];
    if (!readingField && content[i] === '[') {
      readingField = true;
    } else if (readingField && content[i] === ']') {
      readingField = false;
      fields.push(temp.slice(0, -1));
      temp = '';
    }
  }

  const fieldNames = fields.map(underscoresToSpaces);
  console.log(fieldNames);
  return fieldNames.map(fieldName => {
    return templateFields.find(template => template.fieldName === fieldName);
  }).flat();
  // return templateFields.filter(({fieldName}) => fieldNames.includes(fieldName));
}

export function mapStateToTemplateContent(content, state) {
  let mappedContent = content;
  Object.entries(state)
    .map(([key, value]) => [spacesToUnderscores(key), value])
    .forEach(([key, value]) => {
      if (value === undefined) return;
      const startIndex = mappedContent.indexOf(key);
      if (!~startIndex) return;
      const endIndex = startIndex+key.length;
      mappedContent = mappedContent.slice(0, startIndex-1) + value + mappedContent.slice(endIndex+1);
    });
  return mappedContent;
}

export function mapFieldsToString(list, id, mappingFields) {
  // todo
  const item = list.find(item => item.id === id);
  return mappingFields.reduce((memo, field) => {
    return memo + ' ' + (item[field] || '');
  }, '').trim();
}

