import React, {useContext, useState} from 'react';
import './preload.css';
import styles from './style.module.css';
import Container from "react-bootstrap/Container";
import Image from "react-bootstrap/Image";
import cn from 'classnames';
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Wrapper from "../common/core/Wrapper";
import SignFooter from "../common/parts/SignFooter";
import logoImagePath from './images/logo.png';
import withOnImagesLoadedRender from "../common/HOCs/withOnImagesLoadedRender";
import SignInForm from "./SignInForm";
import SignUpForm from "./SignUpForm";
import ElectByIndex from "../common/core/ElectByIndex";
import {POST} from "../../utils/network";
import NetworkContext from "../../DATA_CONTROL/NetworkContext";
import Enum from "../../utils/enum";
import {capitalize} from "../../utils/StringUtils";

const WIDTH = 350;

const [FORM_SIGN_IN, FORM_SIGN_UP] = Enum(2);

function SignIn() {
  const [formIndex, setFormIndex] = useState(0);

  const [loginFailed, setLoginFailed] = useState(false);
  const [loginFailText, setLoginFailText] = useState(null);
  const [loginWaiting, setLoginWaiting] = useState(false);

  const [registerFailed, setRegisterFailed] = useState(false);
  const [registerFailText, setRegisterFailText] = useState(null);
  const [registerSuccess, setRegisterSuccess] = useState(false);
  const [registerWaiting, setRegisterWaiting] = useState(false);

  const networkContext = useContext(NetworkContext);

  // TODO: process different type of errors
  const loginUser = async ({login, password, remember}, {redirect}) => {
    setLoginWaiting(true);
    setLoginFailed(false);

    const res = await POST('/api/Account/signin', {login, password});

    if (res.status === 200) {

      const token = res.body;
      if (remember) {
        localStorage.setItem('token', token);
      } else {
        sessionStorage.setItem('token', token);
      }

      if (redirect) {
        networkContext.onLogin();
      }

    } else if (res.status === 401) {
      setLoginFailed(true);
      setLoginFailText('Incorrect login or password');
    } else {
      setLoginFailed(true);
      setLoginFailText('Unknown error, please try again later');
    }

    setLoginWaiting(false);
  };

  const registerUser = async ({login, password, workemail, name='Name', surname='Surname'}) => {
    setRegisterWaiting(true);
    setRegisterFailed(false);

    const res = await POST('/api/Account/signup', {login, password, workemail, name, surname});

    if (res && res.status === 200) {
      const body = res.body;

      if (body.success) {
        setRegisterFailed(false);
        setRegisterSuccess(true);
        networkContext.addToRegisterTempData({login, password, workemail});
        await loginUser({login, password}, {redirect: false});
      } else {
        setRegisterFailed(true);
        setRegisterFailText(capitalize(body.error));
      }

    } else {
      setRegisterFailed(true);
      setRegisterFailText('Unknown error, please try again later');
    }

    setRegisterWaiting(false);
  };

  const forms = [
    <SignInForm
      onSubmit={(state) => loginUser(state, {redirect: true})}
      failed={loginFailed}
      failText={loginFailText}
      waiting={loginWaiting}
    />,
    <SignUpForm
      onSubmit={registerUser}
      failed={registerFailed}
      failText={registerFailText}
      success={registerSuccess}
      waiting={registerWaiting}
    />,
  ];

  const currentForm = <ElectByIndex
    className={'w-100'}
    index={formIndex}
    children={forms}
  />;

  return (
    <Wrapper className={cn(styles.wrapper)}>

      <Container className={styles.main} style={{width: WIDTH}}>
        <Row>
          <Image className={styles.logoImage} src={logoImagePath}/>
        </Row>

        <Row className={styles.tabs}>
          <Col
            className={cn(styles.tabSignIn, formIndex===FORM_SIGN_IN && styles.tabActive)}
            onClick={() => setFormIndex(FORM_SIGN_IN)}
          >
            Sign In
          </Col>
          <Col
              className={cn(styles.tabSignUp, formIndex===FORM_SIGN_UP && styles.tabActive)}
              onClick={() => setFormIndex(FORM_SIGN_UP)}
          >
            Sign Up
          </Col>
        </Row>

        {currentForm}

      </Container>

      <SignFooter className={styles.footer}/>

    </Wrapper>
  );
}

export default withOnImagesLoadedRender(SignIn);
