import React from 'react';
import styles from './style.module.css';
import {Form, TextArea} from 'informed';
import cn from 'classnames';
import Input from "../../../common/core/Input";
import Col from "react-bootstrap/Col";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import Row from "react-bootstrap/Row";
import ButtonsPair from "../../../common/parts/ButtonsPair";
import {Area} from "../../../SignUp/contents/Business";

const CustomInput = ({title, ...rest}) => (
  <>
    <Col className={cn(styles.title, 'col-12 pl-2 mb-3')}>{title.toUpperCase()}</Col>
    <Col className={'col-12'}>
      <Input heigth={40} {...rest}/>
    </Col>
  </>
);

const CustomForm = ({onSubmit, close}) => (
  <Form onSubmit={onSubmit} className={styles.form}>
    <Row>
      <Col className={'col-10'}>
        <Col className={cn(styles.title, 'col-12 pl-2 mb-3')}>TARGET MARKET</Col>
        <TextArea
          field={'market'}
          placeholder={'Who is your target market, who are your customers? What do they do?'}
          className={styles.area}
        />
      </Col>
    </Row>
    <Row>
      <Col className={'col-10 pt-2'}>
        <CustomInput
          field={'keywords'}
          title={'keywords'}
          placeholder={'Enter comma-separated keywords'}
        />
      </Col>
    </Row>
    <Row>
      <ButtonsPair onCancel={close} className={'pb-2'}/>
    </Row>
  </Form>
);

const AddNewMarket = ({
  onSubmit,
  close,
}) => {

  const TABS = [{
    name: 'ADD NEW TARGET MARKET',
    content: <CustomForm onSubmit={onSubmit} close={close}/>,
  }];

  return (
    <DashboardTabs
      tabs={TABS}
      readyContents={TABS.map(tab => tab.content)}
    />
  );
};

export default AddNewMarket;