import React, {useEffect, useRef, useState} from "react";
import {Option, Select, Text} from "informed";
import styles from './style.module.css';
import cn from 'classnames';
import moment from "moment";

const HEIGHT = 35;

const Input = ({className, height=HEIGHT, required, type='text', containerStyle, containerClassName, ...other}) => {
  const [currentType, setType] = useState(
    type === 'date' ? 'text' : type
  );

  const ref = useRef(null);

  useEffect(() => {
    if (type === 'date' && ref && ref.current && ref.current.value) {
      setType('date');
    }
  }, []);

  return (
    <div
      style={{position: 'relative', ...containerStyle}}
      className={cn(styles.container, containerClassName)}
    >
        {required && <span className={styles.required}>*</span>}
        <Text
            ref={ref}
            className={cn(styles.input, className)}
            autoComplete={"off"}
            style={{height, borderRadius: height / 2}}
            required={required}
            type={currentType}
            onFocus={() => {
                if (type === 'date') {
                    setType('date');
                }
            }}
            {...other}
        />
    </div>
  );
};

export default Input;

export const SelectInput = ({field, height=HEIGHT, placeholder, options, required, containerClassName, ...props}) => (
  <div
    style={{position: 'relative'}}
    className={cn(styles.container, containerClassName)}
  >
    {required && <span className={styles.required}>*</span>}
    <Select
      className={styles.selectInput}
      field={field}
      required={required}
      style={{height, borderRadius: height / 2}}
      {...props}
    >
      <Option value="" disabled>
        {placeholder}
      </Option>
      {options.map(Option)}
    </Select>
  </div>
);

export const DateInput = ({required, containerClassName, ...props}) => (
  <div
    className={cn(styles.container, containerClassName)}
  >
    {required && <span className={styles.required}>*</span>}
    <Input
      parse={(value) => {
        return moment(value).format("YYYY-MM-DDTHH:mm:ss.SSSZ")
      }}
      format={(value) => moment(value).format("YYYY-MM-DD")}
      type={"date"}
      max={'3000-01-01'}
      {...props}
    />
  </div>
);

export const GenderSelect = (props) => (
  <SelectInput
    field={'gender'}
    placeholder={'Gender'}
    options={[
      {value: "0", label: 'Male'},
      {value: "1", label: 'Female'},
    ]}
    {...props}
  />
);
