import React, {useContext} from "react";
import Wrapper from "../../../common/core/Wrapper";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import {activityHighlightColor, getActivitiesByCustomerId, shouldHighlightActivity} from "./Activities";
import {ActivitiesDataContext} from "../../../../DATA_CONTROL/dataContexts";
import cn from 'classnames';
import styles from './style.module.css';

const ActivityCell = ({customerId, createActivityModal}) => {
  const activities = useContext(ActivitiesDataContext);
  const isHighlighted = getActivitiesByCustomerId(activities.data, customerId).some(shouldHighlightActivity);
  return (
    <Wrapper style={{
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: isHighlighted ? activityHighlightColor : 'transparent',
    }}>
      <TouchableOpacity
        onClick={() => createActivityModal({customerId})}
      >
        <i
          className={cn(styles.plusActivity, "fas fa-plus")}
          style={{
            fontSize: 20,
            color: 'rgba(102,102,102,0.5)',
          }}
        />
      </TouchableOpacity>
    </Wrapper>
  );
};

export default ActivityCell;
