import {ListField, StateField} from "./fields";
import {
  createActivity,
  createCustomerB2B, createCustomerB2P,
  createDocument,
  createProduct, createService,
  createTemplate, removeActivity, removeCustomerB2B, removeCustomerB2P, removeDocument, removeProduct, removeService,
  removeTemplate,
  setAccountData,
  setBusinessData,
  setCompanyData, updateActivity, updateCustomerB2B, updateCustomerB2P, updateDocument, updateProduct, updateService,
  updateTemplate
} from "./dataSetters";
import {
  getAccountData, getActivitiesData,
  getBusinessData,
  getCompanyData,
  getCustomerB2BById,
  getCustomerB2PById,
  getCustomersB2BData,
  getCustomersB2PData,
  getDocumentById,
  getDocumentsData,
  getProductsData, getServicesData,
  getTemplateById,
  getTemplateByKeywords,
  getTemplatesData,
  getVariableFieldsData
} from "./dataGetters";
import {useEffect, useState} from "react";

const initialArray = [];

// TODO refactor with reducer
const useApplicationData = () => {
  // let you know if ...
  const [isDataLoaded, setDataLoaded] = useState(false);

  // state fields
  const [account, setAccount] = useState(StateField.default);
  const [business, setBusiness] = useState(StateField.default);
  const [company, setCompany] = useState(StateField.default);

  //
  const [variableFields, setVariableFields] = useState(initialArray);

  // list fields
  const [templates, setTemplates] = useState(ListField.default);
  const [documents, setDocuments] = useState(ListField.default);
  const [products, setProducts] = useState(ListField.default);
  const [services, setServices] = useState(ListField.default);
  const [customersB2B, setCustomersB2B] = useState(ListField.default);
  const [customersB2P, setCustomersB2P] = useState(ListField.default);
  const [activities, setActivities] = useState(ListField.default);

  const getInitialApplicationData = () => (
    Promise.all([
      getAccountData(),
      getCompanyData(),
      getBusinessData(),
      getVariableFieldsData(),
      getTemplatesData(),
      getDocumentsData(),
      getProductsData(),
      getServicesData(),
      getCustomersB2BData(),
      getCustomersB2PData(),
      getActivitiesData(),
    ])
  );

  const setInitialApplicationData = ([
    initAccountData,
    initCompanyData,
    initBusinessData,
    variableFieldsData,
    initTemplatesData,
    initDocumentsData,
    initProductsData,
    initServicesData,
    initCustomersB2BData,
    initCustomersB2PData,
    initActivitiesData,
  ]) => {
    setAccount(new StateField(initAccountData, getAccountData, setAccountData, setAccount));
    setCompany(new StateField(initCompanyData, getCompanyData, setCompanyData, setCompany));
    setBusiness(new StateField(initBusinessData, getBusinessData, setBusinessData, setBusiness));
    setVariableFields(variableFieldsData);
    setTemplates(new ListField({
      initData: initTemplatesData,
      networkListGetter: getTemplatesData,
      networkItemCreator: createTemplate,
      networkItemUpdater: updateTemplate,
      networkItemRemover: removeTemplate,
      networkItemGetterById: getTemplateById,
      networkItemGetterByKeywords: getTemplateByKeywords,
      resolver: setTemplates,
    }));
    setProducts(new ListField({
      initData: initProductsData,
      networkListGetter: getProductsData,
      networkItemCreator: createProduct,
      networkItemUpdater: updateProduct,
      networkItemRemover: removeProduct,
      resolver: setProducts,
    }));
    setServices(new ListField({
      initData: initServicesData,
      networkListGetter: getServicesData,
      networkItemCreator: createService,
      networkItemUpdater: updateService,
      networkItemRemover: removeService,
      resolver: setServices,
    }));
    setDocuments(new ListField({
      initData: initDocumentsData,
      networkListGetter: getDocumentsData,
      networkItemCreator: createDocument,
      networkItemUpdater: updateDocument,
      networkItemGetterById: getDocumentById,
      networkItemRemover: removeDocument,
      resolver: setDocuments,
    }));
    setCustomersB2B(
      new ListField({
        initData: initCustomersB2BData,
        networkListGetter: getCustomersB2BData,
        networkItemCreator: createCustomerB2B,
        networkItemUpdater: updateCustomerB2B,
        networkItemGetterById: getCustomerB2BById,
        networkItemRemover: removeCustomerB2B,
        resolver: setCustomersB2B,
      })
    );
    setCustomersB2P(
      new ListField({
        initData: initCustomersB2PData,
        networkListGetter: getCustomersB2PData,
        networkItemCreator: createCustomerB2P,
        networkItemUpdater: updateCustomerB2P,
        networkItemGetterById: getCustomerB2PById,
        networkItemRemover: removeCustomerB2P,
        resolver: setCustomersB2P,
      })
    );
    setActivities(
      new ListField({
        initData: initActivitiesData,
        networkListGetter: getActivitiesData,
        networkItemCreator: createActivity,
        networkItemUpdater: updateActivity,
        networkItemRemover: removeActivity,
        resolver: setActivities,
      })
    );
  };

  useEffect(() => {
    getInitialApplicationData().then(setInitialApplicationData);
  }, []);

  useEffect(() => {
    if (
      !isDataLoaded &&
      account !== StateField.default &&
      company !== StateField.default &&
      business !== StateField.default &&
      variableFields !== initialArray &&
      templates !== ListField.default &&
      documents !== ListField.default &&
      products !== ListField.default &&
      services !== ListField.default &&
      customersB2B !== ListField.default &&
      customersB2P !== ListField.default &&
      activities !== ListField.default
    ) {
      setDataLoaded(true);
    }
  }, [
    isDataLoaded,
    account, company, business,
    variableFields, templates, documents,
    products, services,
    customersB2B, customersB2P, activities,
  ]);


  return [
    isDataLoaded,
    {
      account,
      company,
      business,
      variableFields,
      templates,
      documents,
      products,
      services,
      customersB2B,
      customersB2P,
      activities,
    }
  ];
};

export default useApplicationData;
