import React, {useEffect} from 'react';
import {HashRouter as Router, Redirect, Route} from 'react-router-dom';
import SignIn from "./SignIn";
import SignUp from "./SignUp";
import Dashboard from "./Dashboard";
import NetworkContext, {useNetworkContextValue} from "../DATA_CONTROL/NetworkContext";

function App() {
  const networkContextValue = useNetworkContextValue();

  useEffect(() => {
    networkContextValue.onStartUp();
  }, []);

  return (
    <NetworkContext.Provider value={networkContextValue}>
      <Router>
        {
          networkContextValue.loggedIn ?
            <Redirect to={'/dashboard'}/>
            :
            <Redirect to={'/login'}/>
        }
        <Route path={`/login`} component={SignIn}/>
        <Route path={`/register`} component={SignUp}/>
        <PrivateRoute
          accessed={networkContextValue.loggedIn}
          path={`/dashboard`}
          component={Dashboard}
        />
      </Router>
    </NetworkContext.Provider>
  );
}

const PrivateRoute = ({component: Component, accessed, ...rest}) => (
  <Route render={props => (
    accessed ?
      <Component {...props}/>
      :
      <Redirect to={'/login'}/>
  )} {...rest}/>
);

export default App;
