import {useState, useEffect, useContext} from 'react';
import {contextShape, DataField, StateField} from "./DataContext";
import {
  getCompetitors,
  getCustomersB2B,
  getCustomersB2P,
  getDocumentsData,
  getMarket,
  getPartners,
  getProductsData,
  getServicesData,
  getTemplatesData, getUserData
} from "./dataGetters";
import NetworkContext from "./NetworkContext";
import {setAccountData} from "./dataSetters";


export default function useDataContextValue() {
  const [dataContextValue, setDataContextValue] = useState(contextShape);

  useEffect(() => {
      const value = {
        // account: new StateField('account', account, setDataContextValue, setAccountData),
        // company: new StateField('company', company, setDataContextValue),
        // business: new StateField('business', business, setDataContextValue),

        // products: new DataField('products', 'code', getProductsData(), setDataContextValue),
        services: new DataField('services', 'id', getServicesData(), setDataContextValue),
        // templates: new DataField('templates','id', getTemplatesData(), setDataContextValue),
        // documents: new DataField('documents','id', getDocumentsData(), setDataContextValue),
        // customersB2B: new DataField('customersB2B', 'code', getCustomersB2B(), setDataContextValue),
        // customersB2P: new DataField('customersB2P', 'code', getCustomersB2P(), setDataContextValue),
        partners: new DataField('partners', 'id', getPartners(), setDataContextValue),
        competitors: new DataField('competitors', 'id', getCompetitors(), setDataContextValue),
        market: new DataField('market', 'id', getMarket(), setDataContextValue),
      };

    setDataContextValue(value);
  }, []);

  return dataContextValue;
}
