import Swagger from 'swagger-client';
import PATHS from '../SETTINGS/PATHS';
import getToken from "./getToken";

const baseURL = PATHS.baseURL;

export const POST = async (path, obj) => {
  const url = baseURL + path;

  const body = JSON.stringify(obj);

  const headers = new Headers();
  headers.append('Content-Type', 'application/json');
  const token = getToken();
  if (token) {
    headers.append('Authorization', `Bearer ${token}`);
  }

  const parameters = {
    method: 'POST',
    headers,
    body,
  };

  const request = {url, ...parameters};
  console.log('POST request', request, obj);

  try {
    const res = await Swagger.http(request);
    console.log('POST response', res);
    return res;
  } catch (err) {
    console.error('POST error', err.response);
    return err.response;
  }
};

export const GET = async (path) => {
  const url = baseURL + path;

  const headers = new Headers();
  headers.append('Content-Type', 'application/json');
  const token = getToken();
  if (token) {
    headers.append('Authorization', `Bearer ${token}`);
  }

  const parameters = {
    method: 'GET',
    headers,
  };

  const request = {url, ...parameters};
  console.log('GET request', request);

  try {
    const res = await Swagger.http(request);
    console.log('GET response', res);
    return res;
  } catch (err) {
    console.error('GET error', err.response);
    return err.response;
  }
};

export class NetworkError {
  constructor(res) {
    console.error('NETWORK ERROR:', res);
    this.res = res;
  }
}
