// import previewPath from './images/preview.png';
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import React from "react";
import cn from 'classnames';
import styles from './style.module.css';
import TouchableOpacity from "../../../../common/core/TouchableOpacity";

// DEV
const itemShape = {
  name: 'file.pdf',
  date: '20.05.2020',
  // preview: '',
};

const items = [...Array(10)].fill(itemShape);

const Item = ({item}) => (
  <div className={cn(styles.itemContainer, 'col-3')}>
    <TouchableOpacity className={cn(styles.item)}>
      <div className={cn(styles.preview)}>
        {/*<TouchableOpacity className={styles.close}><i className={"fas fa-times"}/></TouchableOpacity>*/}
      </div>
      <div className={cn(styles.header)}>
        <div><i className="fas fa-file-pdf mr-2"/>{item.name}</div>
        <div className={styles.date}>{item.date}</div>
      </div>
    </TouchableOpacity>
  </div>
);

const CataloguesTab = () => (
  <Row className={styles.container}>
    {items.map(item => <Item item={item}/>)}
  </Row>
);

export default CataloguesTab;