import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import {ServicesDataContext} from "../../../../DATA_CONTROL/dataContexts";

const columns = [{
  Header: 'CODE#',
  accessor: 'id',
  width: 65,
},{
  Header: 'NAME',
  accessor: 'name',
  width: 120,
},{
  Header: 'PRICE',
  accessor: 'price',
  width: 80,
},{
  Header: 'U.M.',
  accessor: 'unitmeasure',
  width: 80,
},{
  Header: 'GENERAL DESCRIPTION',
  accessor: 'generaldescription',
  minWidth: 250,
},{
  Header: 'DELIVERY',
  accessor: 'supply',
  width: 85,
},{
  Header: 'KEYWORDS',
  accessor: 'keywords',
  width: 100,
}];

function ServicesTab({openEditModal}) {
  const services = useContext(ServicesDataContext);
  return (
    <EditableTable
      data={services.data}
      deleteItem={services.delete}
      editItem={openEditModal}
      columns={columns}
      noWhatText={'services'}
    />
  );
}


export default ServicesTab;
