import React, {useState} from 'react';
import styles from './style.module.css';
import cn from 'classnames';
import TouchableOpacity from "../TouchableOpacity";
import {PulseLoader} from "react-spinners";

const Button = ({
                  title,
                  waiting,
                  onClick=()=>{},
                  className,
                  containerClassName,
                  height,
                  width,
                  color='#1596C0',
                  thin,
                  fontSize='inherit',
                  containerStyle,
                  underlyingColor='transparent',
                  type,
                  textColor='white',
                  loaderSize=7,
                  ...rest
}) => {
  title = typeof title === 'string' ? title.toUpperCase() : title;

  return (
    <TouchableOpacity
      className={cn(styles.container, containerClassName)}
      style={{height, width, ...containerStyle}}
    >
      <button
        className={cn(styles.button, className)}
        onClick={onClick}
        style={{
          backgroundColor: color,
          color: textColor,
          fontWeight: thin ? '400' : '700',
          borderRadius: height && height/2,
          fontSize,
        }}
        type={type}
        {...rest}
      >
        {waiting ? <PulseLoader size={loaderSize} color={textColor}/> : title}
      </button>
    </TouchableOpacity>
  );
};

export default Button;
