import React, {useContext, useEffect, useRef, useState} from "react";
import Wrapper from "../../common/core/Wrapper";
import styles from './style.module.css';
import Row from "react-bootstrap/Row";
import {Col12} from "../../common/core/CustomBootstrap/cols";
import avatarImagePath from '../images/robot.jpg';
import Button from "../../common/core/Button";
import {ScaleLoader} from 'react-spinners';
import getGoogleSpeechTranscript from "./actions/getGoogleSpeechTranscript";
import getGoogleTextToSpeech from "./actions/getGoogleTextToSpeech";
import useEscape from "./useEscape";
import useRecorder from "./useRecorder";
import {
  getEntitiesByTemplateContent,
  mapFieldsToString,
  useFieldFilling
} from "../../../DATA_CONTROL/additional/templateFieldsUtils";
import {TemplatesDataContext} from "../../../DATA_CONTROL/dataContexts";
import {isEmpty, spacesToCommas} from "../../../utils/StringUtils";
import TabContext from "../TAB_CONTROL/TabContext";
import Avatar from "./Avatar";
import Input from "./Input";

export const RECORD_TIME = 3;

// TODO split all into separate hooks, refactor
// TODO solve problem with state variables in onRecordEnd()
const Assistant = ({template:initTemplate=null, close}) => {
  const templates = useContext(TemplatesDataContext);
  const templateOptions = templates.data.map(({id, title}) => ({value: id, label: title}));

  const [template, setTemplate] = useState(initTemplate);
  // dublicated to have access in onRecordEnd callback
  const templateLink = useRef();
  useEffect(() => {
    templateLink.current = template;
  }, [template]);

  const [entities, setEntities] = useState([]);
  const [isTemplateReady, setIsTemplateReady] = useState(false);
  useEffect(() => {
    console.log(template);
    if (template && template.content) {
      const entities = getEntitiesByTemplateContent(template.content);

      // if template has no variable fields
      if (entities.length === 0) {
        setTemplateSelection('Your template has no variable fields. Please select other one.');
        return;
      }

      // const entities = entities.map(getAction);
      // console.log(entities);

      // if some of fields maps to empty list
      const entityWithEmptyList = entities.map(getAction).find(({list}) => list && list.length === 0);
      if (entityWithEmptyList) {
        setTemplateSelection(`You don't have any ${entityWithEmptyList.type} yet. Create one or select other template.`);
        return;
      }

      // all ok!!!
      setIsTemplateReady(true);
      setEntities(entities);
    } else if (template && !template.content) {
      setTemplateSelection('Your template has no content. Would you like to select another one?');
    } else if (!template && templateOptions.length === 0) {
      setTemplateSelection('You have no templates yet. Create one!');
    } else {
      setTemplateSelection();
    }
  }, [template]);

  const getCurrentAction = () => {
    return entities[step] && getAction(entities[step]);
  };

  const setTemplateSelection = (message='Which template do you want to use?') => {
    console.log(message);
    setMultiState(true);
    setInputOptions(templateOptions);
    setMessage(message);
  };

  const trySetTemplate = async () => {
    const id = input.value;
    const template = await templates.getItemById(id);
    setTemplate(template);
  };

  const {getAction, setField, mapStateToContent, selectItem} = useFieldFilling();

  // TODO refactor
  const [step, setStepState] = useState(0);

  const tabContext = useContext(TabContext);
  const previewDocument = () => {
    const content = mapStateToContent(template);
    tabContext.previewDocument(content);
    close();
  };

  const [isFinished, setIsFinished] = useState(false);

  const onFinish = async () => {
    await setMessage('Your document is ready.');
    setIsFinished(true);
  };

  const submitSelection = (shouldSaveInputValue) => {
    console.log(input);
    stopSound();
    // if we click on submit but nave no value
    if (shouldSaveInputValue && isEmpty(input)) return;

    // template selection stage
    if (!isTemplateReady) {
      trySetTemplate();
      return;
    }

    if (shouldSaveInputValue) {
      saveInputValue();
    }

    if (step === entities.length-1) {
      onFinish();
      return;
    }

    setStepState(step => step+1);
  };

  const saveInputValue = () => {
    const {fieldName, list, mappingFields, type} = getCurrentAction();
    if (list) {
      const id = input.value;
      const value = mapFieldsToString(list, id, mappingFields);
      selectItem(type, id);
      setField({[fieldName]: value});
    } else {
      const value = input.value;
      setField({[fieldName]: value});
    }
  };

  useEffect(() => {
    const action = getCurrentAction();
    if (!action) return;

    const {fieldName, value, question, list, selectQuestion, type, mappingFields, select} = action;

    if (value !== undefined) {
      setField({[fieldName]: value});
      submitSelection(false);
      return;
    }

    if (list) {
      setMultiState(true);
      if (select && select[type] !== undefined) {
        // already selected item of this type -> use it
        const id = select[type];
        const value = mapFieldsToString(list, id, mappingFields);
        setField({[fieldName]: value});
        submitSelection(false);
        return;
      }
      // setInput('Select...');
      setInput({});
      //todo
      setInputOptions(list.map(({id, name}) => ({value: id, label: name})));
      setMessage(selectQuestion);
    } else {
      setMultiState(false);
      setInput({});
      setInputOptions([]);
      setMessage(question);
    }
  }, [step, entities]);

  const [input, setInput] = useState({});
  const [inputOptions, setInputOptions] = useState([]);

  const [isMultiState, setMultiState] = useState(false);
  // dublicated to have access in onRecordEnd callback
  const isMulti = useRef();
  useEffect(() => {
    isMulti.current = isMulti;
  }, [isMulti]);

  const [thinking, setThinking] = useState(false);
  const [message, setMessageState] = useState(null);
  const [sound, setSound] = useState(null);
  const playSound = () => sound && sound.play();
  const stopSound = () => sound && sound.pause();
  useEffect(() => {
    playSound();
  }, [sound]);

  const setMessage = async (message) => {
    console.log(message);
    const base64data = await getGoogleTextToSpeech(message);
    const sound = new Audio("data:audio/MP3;base64," + base64data);
    setSound(sound);
    setMessageState(message);
  };

  const onRecordEnd = async (base64data) => {
    setRecording(false);
    setThinking(true);
    const transcript = await getGoogleSpeechTranscript(base64data);

    if (!transcript) {
      setThinking(false);
      setMessage('Can you repeat please?');
      return;
    }

    // todo refactor
    if (isMulti.current) {
      const keywords = transcript.split(' ');
      const view = spacesToCommas(transcript);
      if (!templateLink.current || !templateLink.current.content) {
        const foundedTemplate = await templates.getItemByKeywords(keywords);
        if (foundedTemplate) {
          setMessage('I found this by keywords: ' + view);
          setInput({value: foundedTemplate.id, label: foundedTemplate.title});
        } else {
          setMessage('Sorry, I found nothing by keywords: ' + view);
        }
      } else {
        // TODO all by keywords
        setMessage('I found this by keywords: ' + view);
        setInput({value: 0, label: 'Default'});
      }
    } else {
      setInput({value: transcript, label: transcript});
    }

    setThinking(false);
  };

  const [recordDisabled, setRecordDisabled] = useState(false);
  const onMicrophoneAccessGranted = () => {
    setRecordDisabled(false);
  };
  const onMicrophoneAccessDenied = () => {
    setRecordDisabled(true);
  };

  const [recStart, recForceStop] = useRecorder(onRecordEnd, onMicrophoneAccessGranted, onMicrophoneAccessDenied);

  const [recording, setRecording] = useState(false);
  useEffect(() => {
    if (recording) recStart(RECORD_TIME, step);
  }, [recording]);

  const startRecord = () => {
    setMessageState('');
    setRecording(true);
  };

  const stopRecord = () => {
    setRecording(false);
    recForceStop(step);
  };

  const containerRef = useEscape(styles.container, () => {
    stopSound();
    close();
  });

  return (
    <Wrapper ref={containerRef} className={styles.container}>
      <Row className={styles.box}>
        <Avatar imagePath={avatarImagePath} thinking={thinking}/>
        <Dialog listening={recording} question={message}/>
        {!isFinished && (
          <Input
            listening={recording}
            value={input}
            setInput={setInput}
            recordDisabled={recordDisabled}
            options={inputOptions}
            onMicrophoneClick={recording ? stopRecord : startRecord}
            onSubmit={() => submitSelection(true)}
            isMulti={isMultiState}
          />
        )}
        {isFinished && (
          <Button
            title={'preview'}
            onClick={previewDocument}
            width={150}
            containerClassName={styles.previewButton}
          />
        )}
      </Row>
    </Wrapper>
  );
};

const Dialog = ({listening, question}) => (
  <Col12 className={styles.row}>
    {listening ?
      <ScaleLoader
        height={20}
        width={4}
        color={'#ffffff'}
      />
      :
      <span className={styles.question}>{question}</span>
    }
  </Col12>
);

export default Assistant;
