import React, {useState, useEffect, useRef} from "react";
import ReactTable from 'react-table';
import "react-table/react-table.css";
import styles from './style.module.css';
import cn from 'classnames';
import './reactTableStyle.css';
import deleteIconPath from './icons/delete.png';
import editIconPath from './icons/edit.png';
import Image from "react-bootstrap/Image";
import TouchableOpacity from "../../core/TouchableOpacity";
import {activityHighlightColor, shouldHighlightActivity} from "../../../Dashboard/contents/Customers/Activities";
import Button from "../../core/Button";
import useWaiting from "../../hooks/useWaiting";

// doublicated in reactTableStyle
const ROW_HEIGHT = 50;

// TODO limit letters count
const Cell = ({editable, className, deletingPlaceholder, saveData, bufferValue, colIndex, format, ...props}) => {
  const cellEl = useRef(null);

  const onEnter = (e) => {
    if (e.keyCode === 13) e.target.blur();
  };

  useEffect(() => {
    if (!cellEl.current) return;
    cellEl.current.addEventListener('keydown', onEnter);
    return () => cellEl.current.removeEventListener('keydown', onEnter);
  });

  const value = bufferValue ? bufferValue : props.value;

  return deletingPlaceholder ? deletingPlaceholder
    : (<div
        ref={cellEl}
        contentEditable={editable}
        suppressContentEditableWarning
        onClick={() => console.log(props)}
        onBlur={saveData}
        className={cn(styles.cell, editable && styles.editable, className)}
      >
        {format ? format(value) : value}
      </div>);
};

const ControlCell = ({setEditingItem, saveBufferToData, cancelEdit, deleteItem, editing}) => (
  <div className={cn(styles.cell, styles.controlCell)}>
    <TouchableOpacity>
      {editing ?
        <div onClick={saveBufferToData} className={styles.save}><i className={"fas fa-check"}/></div>
        :
        <Image onClick={setEditingItem} src={editIconPath} height={20} className={styles.controlIcon}/>
      }
    </TouchableOpacity>
    <TouchableOpacity>
      {editing ?
        <div onClick={cancelEdit} className={styles.cancel}><i className="fas fa-ban"/></div>
        :
        <Image onClick={deleteItem} src={deleteIconPath} height={20} className={styles.controlIcon}/>
      }
    </TouchableOpacity>
  </div>
);

const EditableTable = ({
  data,
  noWhatText='data',
  columns: inputColumns,
  deleteItem: deleteDataItem = async () => {},
  editItem: editDataItem = async () => {},
  headerClassName,
  ...otherProps
}) => {
  // layout
  // TODO page size!
  const getPageSize = () => Math.floor((document.documentElement.clientHeight - 240)/ROW_HEIGHT);
  const [pageSize, setPageSize] = useState(getPageSize());

  const onResize = () => setPageSize(getPageSize());

  useEffect(() => {
    window.addEventListener('resize', onResize);
    return () => window.removeEventListener('resize', onResize);
  }, []);

  // editing
  // 1) set item and its data by code
  // 2) change its data with buffer while editing
  // 3) save buffer in data store (if saved)
  const [editingItem, setEditingItemState] = useState({});

  const setEditingItem = (id) => {
    const buffer = data.find(item => item.id === id);
    setEditingItemState({id, buffer});
  };

  const addColumnToBuffer = (id, text) => {
    // TODO check [] syntax with Object.assign
    const buffer = {...editingItem.buffer};
    buffer[id] = text;
    setEditingItemState({id: editingItem.id, buffer});
  };

  const saveBufferToData = async () => {
    await editDataItem(editingItem.buffer);
    cancelEdit();
  };

  const cancelEdit = () => setEditingItemState({});

  // deleting
  const [waits, setWaits] = useState([]);

  const addWait = (code) => {
    setWaits([...waits, code]);
  };

  const removeWait = (id) => {
    setWaits(waits.filter(item => item.id === id));
  };

  const deleteItem = async (id) => {
    await deleteDataItem(id);
    removeWait(id);
  };

  // TODO refactor !!!!!! just 2 healthy components!
  const columns = inputColumns
    .map(item => ({
      Cell: ({original, column, ...props}) => {
        const {id: cellId} = original;
        const {id: columnName} = column;

        // if item is waiting for delete, create placeholder to replace cells
        const deletingPlaceholder =
          columnName === 'id' &&
          waits.includes(cellId) &&
          <DeletingPlaceholder
            id={cellId}
            cancel={() => removeWait(cellId)}
            submit={async () => await deleteItem(cellId)}
          />;

        return (
          <Cell
            editable={columnName !== 'id' && editingItem.id === cellId}
            deletingPlaceholder={deletingPlaceholder}
            saveData={(e) => addColumnToBuffer(columnName, e.target.innerText)}
            bufferValue={cellId === editingItem.id && editingItem.buffer[columnName]}
            colIndex={column.index}
            format={item.format}
            {...props}
          />
        );
      },
      headerClassName,
      ...item
    }))
    .concat({
      Cell: ({original}) => (
        <ControlCell
          // setEditingItem={() => setEditingItem(original.id)}
          // TODO remove all old code after you sure you dont need it
          setEditingItem={() => editDataItem(original)}
          saveBufferToData={saveBufferToData}
          cancelEdit={cancelEdit}
          code={original.id}
          editing={editingItem.id === original.id}
          deleteItem={() => addWait(original.id)}
        />
      ),
      headerClassName,
      width: 80,
    });


  // horizontal scrolling
  function onWheel(e) {
    this.scrollLeft += e.deltaY*0.33;
  }

  const containerRef = useRef();
  useEffect(() => {
    if (!containerRef) return;
    const table = containerRef.current.querySelector('.rt-table');
    table.addEventListener('wheel', onWheel);
    return () => table.removeEventListener('wheel', onWheel);
  }, [containerRef]);

  return (
    <div ref={containerRef} className={styles.container}>
    <ReactTable
      data={data}
      columns={columns}
      showPageSizeOptions={false}
      sortable={false}
      noDataText={`No ${noWhatText} yet`}
      // resizable={false}

      NextComponent={Right}
      PreviousComponent={Left}
      // TODO change!
      minRows={pageSize}
      // TODO change!
      pageSize={pageSize}

      getTrProps={(state, rowInfo) => {
        if (!rowInfo) return {};

        const {original} = rowInfo;

        const isEditing = original.id === editingItem.id;

        // todo extract
        const isHighlighted = original.nextactiondate && shouldHighlightActivity(original);
        const backgroundColor = isHighlighted ? activityHighlightColor : 'transparent';

        return {
          style: {
            backgroundColor: isEditing ? '#ECF4F7' : backgroundColor,
          }
        };
      }}

      defaultSorted={[
        {
          id: 'id',
          desc: true,
        }
      ]}

      {...otherProps}
    />
    </div>
  );
};

const Left = ({children , ...props}) => (
  <TouchableOpacity><i className="fas fa-chevron-left" {...props}/></TouchableOpacity>
);

const Right = ({children , ...props}) => (
  <TouchableOpacity><i className="fas fa-chevron-right" {...props}/></TouchableOpacity>
);


const buttonHeight = ROW_HEIGHT/2;
const buttonWidth = 100;
const DeletingPlaceholder = ({id, cancel, submit}) => {
  const [waiting, onSubmit] = useWaiting(submit);
  return (
    <div className={styles.placeholder}>
      You are about to delete item <span className={styles.code}>#{id}</span>
      <Button
        title={'delete'}
        color={'rgba(114,28,36,0.6)'}
        onClick={onSubmit}
        waiting={waiting}
        height={buttonHeight}
        fontSize={buttonHeight/1.75}
        width={buttonWidth}
        containerClassName={'ml-2 mr-2'}
      />
      <Button
        title={'cancel'}
        color={'#929493'}
        onClick={cancel}
        height={buttonHeight}
        fontSize={buttonHeight/1.75}
        width={buttonWidth}
      />
    </div>
  );
};

export default EditableTable;
