import React, {useState, useRef} from "react";
import Wrapper from "../common/core/Wrapper";
import Menu from "./Menu";
import styles from './style.module.css';
import Header from "./Header";
import withOnImagesLoadedRender from "../common/HOCs/withOnImagesLoadedRender";
import Templates from "./contents/Templates";
import EditUserInfo from "./UserSettings";
import Products from "./contents/Products";
import Documents from "./contents/Documents";
import Services from "./contents/Services";
import Customers from "./contents/Customers";
import DataContext from '../../DATA_CONTROL/DataContext';
import TabContext from './TAB_CONTROL/TabContext';
import useDataContextValue from "../../DATA_CONTROL/useDataContextValue";
import {useTabContextValue} from "./TAB_CONTROL/TabContext";
import Partners from "./contents/Partners";
import Market from "./contents/Market";
import Competitors from "./contents/Competitors";
import ElectByIndex from "../common/core/ElectByIndex";
import {
  AccountDataContext, ActivitiesDataContext,
  BusinessDataContext,
  CompanyDataContext,
  CustomerB2BDataContext,
  CustomerB2PDataContext,
  DocumentsDataContext,
  ProductsDataContext, ServicesDataContext,
  TemplatesDataContext,
  VariableFieldsDataContext
} from "../../DATA_CONTROL/dataContexts";
import useApplicationData from "../../DATA_CONTROL/useApplicationData";
import {PulseLoader} from 'react-spinners';
import {AssistantContext, useAssistantContextValue} from "./TAB_CONTROL/AssistantContext";

// DEV
const HEADER_HEIGHT = 60;
const LEFT_SIDE_WIDTH = 250;

function Dashboard() {
  // data
  const [isDataLoaded, {
    account,
    company,
    business,
    variableFields,
    templates,
    products,
    services,
    documents,
    customersB2B,
    customersB2P,
    activities,
  }] = useApplicationData();

  return isDataLoaded ? (
      <AccountDataContext.Provider value={account}>
      <CompanyDataContext.Provider value={company}>
      <BusinessDataContext.Provider value={business}>
      <VariableFieldsDataContext.Provider value={variableFields}>
      <TemplatesDataContext.Provider value={templates}>
      <ProductsDataContext.Provider value={products}>
      <ServicesDataContext.Provider value={services}>
      <DocumentsDataContext.Provider value={documents}>
      <CustomerB2BDataContext.Provider value={customersB2B}>
      <CustomerB2PDataContext.Provider value={customersB2P}>
      <ActivitiesDataContext.Provider value={activities}>

        <DashboardContent/>

      </ActivitiesDataContext.Provider>
      </CustomerB2PDataContext.Provider>
      </CustomerB2BDataContext.Provider>
      </DocumentsDataContext.Provider>
      </ServicesDataContext.Provider>
      </ProductsDataContext.Provider>
      </TemplatesDataContext.Provider>
      </VariableFieldsDataContext.Provider>
      </BusinessDataContext.Provider>
      </CompanyDataContext.Provider>
      </AccountDataContext.Provider>
  ) : (
      <Loading/>
  );
}

const Loading = () => (
    <Wrapper style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
      <PulseLoader
        color={'#554590'}
        size={10}
      />
    </Wrapper>
);

const DashboardContent = () => {
  // assistant
  const assistant = useAssistantContextValue();

  // layout
  const tabContextValue = useTabContextValue();
  const [modalIndex, setModalIndex] = useState(null);
  const setActiveTabIndex = (index) => {
    tabContextValue.setTabIndex(index);
    setModalIndex(null);
  };
  const TABS = useRef([{
      name: 'Templates',
      content: <Templates/>,
    },{
      name: 'Documents',
      content: <Documents/>,
    },{
      name: 'Products',
      content: <Products/>,
    },{
      name: 'Services',
      content: <Services/>,
    },{
      name: 'Target market',
      content: <Market/>,
    },{
      name: 'Customers',
      content: <Customers/>,
    },{
      name: 'Partners',
      content: <Partners/>,
    },{
      name: 'Competitors',
      content: <Competitors/>,
  }]);

  const tabs = <ElectByIndex
    index={tabContextValue.tabIndex}
    className={styles.tabContainer}
  >
    {TABS.current.map(tab => tab.content)}
  </ElectByIndex>;

  const MODALS = [
    <EditUserInfo close={() => setModalIndex(null)} showing={modalIndex===0}/>,
  ];

  const content = <ElectByIndex
    index={modalIndex}
    className={styles.tabContainer}
    defaultComponent={tabs}
  >
    {MODALS}
  </ElectByIndex>;

  const dataContextValue = useDataContextValue();

  return (
    <DataContext.Provider value={dataContextValue}>
      <TabContext.Provider value={tabContextValue}>
        <AssistantContext.Provider value={assistant}>
          <Wrapper className={styles.wrapper}>
            <Header
              height={HEADER_HEIGHT}
              leftSideWidth={LEFT_SIDE_WIDTH}
              openAssistant={() => assistant.open()}
            />
            <div className={styles.main}>
              <Menu
                items={TABS.current}
                activeTabIndex={tabContextValue.tabIndex}
                setTabIndex={setActiveTabIndex}
                showUserInfo={() => setModalIndex(0)}
                width={LEFT_SIDE_WIDTH}
              />
              {content}
            </div>
          </Wrapper>
          {assistant.assistant}
        </AssistantContext.Provider>
      </TabContext.Provider>
    </DataContext.Provider>
  );
};

export default withOnImagesLoadedRender(Dashboard);
