import {useState} from "react";

const useWaiting = (func) => {
  const [waiting, setWaiting] = useState(false);

  const waitingFunc = async (...args) => {
    setWaiting(true);
    await func(...args);
    setWaiting(false);
  };

  return [waiting, waitingFunc];
};

export default useWaiting;
