import React from 'react';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Input, {GenderSelect} from "../../common/core/Input";
import styles from '../style.module.css';
import {Scope} from 'informed';
import moment from "moment";

export default () => (
  <Row>
    <Col className={"col-6 m-0 pr-2 text-center"}>
      <Col className={styles.subheader}>
        Introduce your personal data
      </Col>

      <Input field={'name'} placeholder={'Name'} required className={"mt-4"}/>
      <Input field={'surname'} placeholder={'Surname'} required/>
      <Input field={'dateofbirth'} parse={(value) => moment(value).format("YYYY-MM-DDTHH:mm:ss.SSSZ")} placeholder={'Date of birth'} required type={"date"} max={'3000-01-01'}/>
      <GenderSelect/>
    </Col>

    <Col className={"col-6 m-0 pl-2 text-center"}>
      <Col className={styles.subheader}>
        Address
      </Col>

      <Scope scope={'address'}>
        <Row className={"mt-4"}>
          <Col className={"col-8 pr-2"}>
            <Input field={'street'} placeholder={'Street'}/>
            <Input field={'city'} placeholder={'City'}/>
            <Input field={'country'} placeholder={'Country'}/>
          </Col>
          <Col className={"col-4 pl-2"}>
            <Input field={'building'} placeholder={'№'}/>
            <Input field={'index'} placeholder={'Index'}/>
            <Input field={'state'} placeholder={'State'}/>
          </Col>
        </Row>
      </Scope>

      {/*<Input field={'workemail'} placeholder={'Work email'}/>*/}
      <Input field={'alternativermail'} placeholder={'Alternative email'} type={'email'}/>
      <Input field={'facebook'} placeholder={'Facebook profile'}/>
      <Input field={'twitter'} placeholder={'Twitter profile'}/>
      <Input field={'linkedin'} placeholder={'Linked-in profile'}/>
    </Col>

  </Row>
);

