import React from "react";
import {useEffect, useReducer} from 'react';
import Enum from "../../../utils/enum";

const contextShape = {
  tabIndex: 0,
  isCreatingDocument: false,
  documentContent: '',
  setTabIndex: () => {},
  previewDocument: () => {},
};

const [
  TEMPLATES,
  DOCUMENTS,
  PRODUCTS,
  SERVICES,
  TARGET_MARKET,
  CUSTOMERS,
  PARTNERS,
  COMPETITORS,
] = Enum(8);

const [
  SET_TAB_INDEX,
  PREVIEW_DOCUMENT_START,
  PREVIEW_DOCUMENT_END,
  NEW_STATE
] = Enum(4);

function reducer(state, {type, tabIndex, documentContent, newState}) {
  switch (type) {
    case SET_TAB_INDEX:
      return {...state, tabIndex};
    case PREVIEW_DOCUMENT_START:
      console.log(documentContent);
      return {
        ...state,
        isCreatingDocument: true,
        tabIndex: DOCUMENTS,
        documentContent,
      };
    case PREVIEW_DOCUMENT_END:
      return {
        ...state,
        isCreatingDocument: false,
        documentContent: '',
      };
    case NEW_STATE:
      return {...newState};
    default:
      return state;
  }
}

export function useTabContextValue() {
  const [state, dispatch] = useReducer(reducer, contextShape);

  useEffect(() => {
    const newState = {
      tabIndex: TEMPLATES,
      setTabIndex: (tabIndex) => dispatch({type: SET_TAB_INDEX, tabIndex}),
      isCreatingDocument: false,
      documentContent: '',
      previewDocument: (documentContent) => {
        dispatch({
          type: PREVIEW_DOCUMENT_START,
          documentContent,
        });
      },
      endPreview: () => {
        dispatch({
          type: PREVIEW_DOCUMENT_END,
        });
      },
    };

    dispatch({type: NEW_STATE, newState});
  }, []);

  return state;
}

const TabContext = React.createContext(contextShape);

export default TabContext;
