import Row from "react-bootstrap/Row";
import styles from "./style.module.css";
import Image from "react-bootstrap/Image";
import logoImagePath from "./images/logo.png";
import Col from "react-bootstrap/Col";
import Button from "../common/core/Button";
import microphoneImagePath from "./images/icons/microphone.png";
import lensImagePath from "./images/icons/lens.png";
import {Form, Text} from "informed";
import React from "react";

export default ({height, leftSideWidth, openAssistant}) => (
  <Row className={styles.header} style={{height}}>
    <div
      className={styles.logoContainer}
      style={{width: leftSideWidth}}
    >
      <Image height={height} src={logoImagePath}/>
    </div>
    <Col className={styles.headerContentContainer}>
      <Search/>
      <Assistance openAssistant={openAssistant}/>
    </Col>
  </Row>
);

const Assistance = ({openAssistant}) => (
  <div className={styles.assistanceContainer}>
    <Button
      height={40}
      title={
        <div className={styles.assistanceTitle}>
          <div>FASTBOSS ASSISTANCE</div>
          <div><Image height={25} src={microphoneImagePath}/></div>
        </div>
      }
      onClick={openAssistant}
    />
  </div>
);

const Search = () => {
  // DEV
  const onSubmit = (data) => alert(data.field);

  return (
    <div className={styles.searchContainer}>
      <Form onSubmit={onSubmit}>
        <Text placeholder={'Search'} field={'field'} className={styles.searchInput}/>
      </Form>
      <Image height={20} className={styles.searchLensImage} src={lensImagePath}/>
    </div>
  );
};