import React, {useEffect, useRef} from "react";

function animate(options) {

  const start = performance.now();

  requestAnimationFrame(function animate(time) {
    // timeFraction от 0 до 1
    let timeFraction = (time - start) / options.duration;
    if (timeFraction > 1) timeFraction = 1;

    // текущее состояние анимации
    const progress = options.timing(timeFraction);

    options.draw(progress);

    if (timeFraction < 1) {
      requestAnimationFrame(animate);
    }

  });
}

const LINE_WIDTH = 2;

const StopButton = ({radius, className, seconds}) => {
  const canvasRef = useRef();
  useEffect(() => {
    if (!canvasRef) return;
    const ctx = canvasRef.current.getContext('2d');
    ctx.lineWidth = LINE_WIDTH;
    ctx.strokeStyle = 'rgba(255, 255, 255, 0.9)';
    ctx.fillStyle = 'rgba(255, 255, 255, 0.9)';
    const R = radius-LINE_WIDTH/2;
    const RECT_SIDE = 2*radius/3;
    const OFFSET = radius - RECT_SIDE/2;
    animate({
      timing: time => time,
      draw: (progress) => {
        ctx.clearRect(0, 0, radius*2, radius*2);
        ctx.beginPath();
        ctx.arc(radius, radius, R, -Math.PI/2, 2*Math.PI*progress-Math.PI/2, false);
        ctx.fillRect(OFFSET, OFFSET, RECT_SIDE, RECT_SIDE);
        ctx.stroke();
      },
      duration: seconds*1000,
    });
  }, [canvasRef]);

  return <canvas width={2 * radius} height={2 * radius} ref={canvasRef} className={className}/>;
};

export default StopButton;