import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import styles from "./style.module.css";
import ActivityCell from "./ActivityCell";
import {CustomerB2PDataContext} from "../../../../DATA_CONTROL/dataContexts";
import {buildString, formatDate} from "../../../../utils/StringUtils";

function CustomersB2P ({createActivityModal, openEditModal}) {
  const customersB2P = useContext(CustomerB2PDataContext);

  const columns = [{
    Header: 'CODE#',
    accessor: 'id',
    width: 65,
    index: 0,
  },{
    Header: 'NAME',
    accessor: 'name',
    width: 120,
    index: 1,
  },{
    Header: 'ACTIVITIES',
    Cell: ({original}) => (
      <ActivityCell
        createActivityModal={createActivityModal}
        customerId={original.id}
      />
    ),
    width: 90,
    index: 10,
  },{
    Header: 'ADDRESS',
    accessor: 'fulladdress',
    // format: ({id, ...value}) => buildString(value),
    width: 120,
    index: 2,
  },{
    Header: 'EMAIL',
    accessor: 'email',
    width: 150,
    index: 3,
  },{
    Header: 'PHONE',
    accessor: 'phone',
    width: 100,
    index: 4,
  },{
    Header: 'WHAT DOES HE/SHE DO? (PROFESSION)',
    accessor: 'directionactivity',
    minWidth: 150,
    index: 5,
  },{
    Header: 'FACEBOOK PROFILE',
    accessor: 'facebookprofile',
    width: 120,
    index: 6,
  },{
    Header: 'DATE OF BIRTH',
    accessor: 'dateofbirth',
    format: formatDate,
    width: 100,
    index: 7,
  },{
    Header: 'WHAT DOES HE/SHE BOUGHT?',
    accessor: 'buyedlasttime',
    minWidth: 150,
    index: 8,
  },{
    Header: 'KEYWORDS',
    accessor: 'keywords',
    width: 100,
    index: 9,
  }];

  return (
    <EditableTable
      data={customersB2P.data}
      deleteItem={customersB2P.remove}
      editItem={openEditModal}
      columns={columns}
      headerClassName={styles.tableHeader}
      noWhatText={'customers'}
    />
  );
}

export default CustomersB2P;
