import React from 'react';
import styles from './style.module.css';
import {Form} from 'informed';
import cn from 'classnames';
import Input from "../../../common/core/Input";
import Col from "react-bootstrap/Col";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import Row from "react-bootstrap/Row";
import ButtonsPair from "../../../common/parts/ButtonsPair";

const CustomInput = ({title, ...rest}) => (
  <>
    <Col className={cn(styles.title, 'col-12 pl-2 mb-3')}>{title.toUpperCase()}</Col>
    <Col className={'col-12'}>
      <Input heigth={40} {...rest}/>
    </Col>
  </>
);

const CustomForm = ({onSubmit, close}) => (
  <Form onSubmit={onSubmit} className={styles.form}>
    <Row>
      <Col className={'col-4 pr-2'}>
        <CustomInput
          field={'name'}
          title={'name of competitor'}
        />
      </Col>
      <Col className={'col-6 pl-2'}>
        <CustomInput
          field={'address'}
          title={'address'}
        />
      </Col>
    </Row>
    <Row className={'pt-2'}>
      <Col className={'col-4 pr-2'}>
        <CustomInput
          field={'phone'}
          title={'phone'}
        />
      </Col>
      <Col className={'col-6 pl-2'}>
        <CustomInput
          field={'email'}
          title={'email'}
        />
      </Col>
    </Row>
    <Row>
      <Col className={'col-10 pt-2'}>
        <CustomInput
          field={'specialization'}
          title={'what does their company do?'}
        />
      </Col>
      <Col className={'col-10 pt-2'}>
        <CustomInput
          field={'customers'}
          title={'who are their customers?'}
        />
      </Col>
      <Col className={'col-10 pt-2 pr-2'}>
        <CustomInput
          field={'facebook'}
          title={'business facebook page link'}
        />
      </Col>
      <Col className={'col-10 pt-2'}>
        <CustomInput
          field={'keywords'}
          title={'keywords'}
          placeholder={'Enter comma-separated keywords'}
        />
      </Col>
    </Row>
    <Row>
      <ButtonsPair onCancel={close} className={'pb-2'}/>
    </Row>
  </Form>
);

const AddNewCompetitor = ({
  onSubmit,
  close,
}) => {

  const TABS = [{
    name: 'ADD NEW COMPETITOR',
    content: <CustomForm onSubmit={onSubmit} close={close}/>,
  }];

  return (
    <DashboardTabs
      tabs={TABS}
      readyContents={TABS.map(tab => tab.content)}
    />
  );
};

export default AddNewCompetitor;