import Col from "react-bootstrap/Col";
import cn from 'classnames';
import React from "react";

// TODO боже только не забудь убрать это позорище

export const Col12 = ({className, children, ...props}) => (
  <Col className={cn('col-12', className)} {...props}>{children}</Col>
);

export const Col10 = ({className, children, ...props}) => (
  <Col className={cn('col-10', className)} {...props}>{children}</Col>
);

export const Col4 = ({className, children, ...props}) => (
  <Col className={cn('col-4', className)} {...props}>{children}</Col>
);

export const Col2 = ({className, children, ...props}) => (
  <Col className={cn('col-2', className)} {...props}>{children}</Col>
);

