import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import styles from './style.module.css';
import ActivityCell from "./ActivityCell";
import {CustomerB2BDataContext} from "../../../../DATA_CONTROL/dataContexts";
import {buildString} from "../../../../utils/StringUtils";

function CustomersB2B ({createActivityModal, openEditModal}) {
  const customersB2B = useContext(CustomerB2BDataContext);

  const columns = [{
    Header: 'CODE#',
    accessor: 'id',
    width: 65,
    index: 0,
  },{
    Header: 'NAME',
    accessor: 'name',
    width: 120,
    index: 1,
  },{
    Header: 'ACTIVITIES',
    Cell: ({original}) => (
      <ActivityCell
        createActivityModal={createActivityModal}
        customerId={original.id}
      />
    ),
    width: 90,
    index: 10,
  },{
    Header: 'ADDRESS',
    accessor: 'fulladdress',
    // format: ({id, ...value}) => buildString(value),
    width: 120,
    index: 2,
  },{
    Header: 'EMAIL',
    accessor: 'email',
    width: 150,
    index: 3,
  },{
    Header: 'PHONE',
    accessor: 'phone',
    width: 100,
    index: 4,
  },{
    Header: 'WHAT DOES THEIR COMPANY DO?',
    accessor: 'directionactivity',
    minWidth: 140,
    index: 5,
  },{
    Header: 'WHAT DOES THEIR COMPANY NEED?',
    accessor: 'needs',
    minWidth: 140,
    index: 6,
  },{
    Header: 'PERSON WHO DECIDE',
    accessor: 'responsibleforpurchasing',
    width: 110,
    index: 7,
  },{
    Header: 'WHAT DID THEY TOOK LAST TIME?',
    accessor: 'buyedlasttime',
    minWidth: 140,
    index: 8,
  },{
    Header: 'KEYWORDS',
    accessor: 'keywords',
    width: 100,
    index: 9,
  }];

  return (
    <EditableTable
      data={customersB2B.data}
      deleteItem={customersB2B.remove}
      editItem={openEditModal}
      columns={columns}
      headerClassName={styles.tableHeader}
      noWhatText={'customers'}
    />
  );
}

export default CustomersB2B;
