import React, {useContext, useEffect, useState} from "react";
import {Col12, Col4} from "../../../common/core/CustomBootstrap/cols";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import Image from "react-bootstrap/Image";
import clipboardBlueImagePath from "../../images/icons/templates/clipboard_blue.png";
import templatesStyles from "../Templates/Main/style.module.css";
import styles from './style.module.css';
import Row from "react-bootstrap/Row";
import cn from "classnames";
import Button from "../../../common/core/Button";
import moment from "moment";
import Editor from "../Templates/Editor";
import ElectById from "../../../common/core/ElectById";
import TabContext from "../../TAB_CONTROL/TabContext";
import {DocumentsDataContext} from "../../../../DATA_CONTROL/dataContexts";

const Documents = () => {
  const documents = useContext(DocumentsDataContext);

  const {isCreatingDocument, documentContent, endPreview} = useContext(TabContext);

  useEffect(() => {
    if (isCreatingDocument) {
      setActiveElementId('editor');
    } else {
      setActiveElementId('main')
    }
  }, [isCreatingDocument]);

  const [activeElementId, setActiveElementId] = useState('main');

  const openMain = () => setActiveElementId('main');
  const openEditor = () => setActiveElementId('editor');

  const saveEditor = async (document) => {
    await documents.create(document);
  };

  const closeEditor = () => {
    openMain();
    endPreview();
  };

  const ELEMENTS = [{
    id: 'main',
    component: <DocumentList
      openEditor={openEditor}
    />,
  },{
    id: 'editor',
    component: <Editor
      onSave={saveEditor}
      onClose={closeEditor}
      defaultHtmlText={isCreatingDocument ? documentContent : ''}
      withFields={false}
      type={'document'}
    />,
  }];

  return (
    <ElectById
      activeElementId={activeElementId}
      containerClassName={styles.wrapper}
    >
      {ELEMENTS}
    </ElectById>
  );
};

const DocumentList = ({openEditor}) => {
  const documents = useContext(DocumentsDataContext);
  return (
    <div className={styles.container}>
      <Row className={styles.header}>
        <div className={styles.name}>YOUR DOCUMENTS</div>
        <div>
          <Button
            thin
            onClick={openEditor}
            title={'create new'}
            height={28}
            width={130}
          />
        </div>
      </Row>
      <Row className={styles.documentsList}>
        {documents.data.length > 0 ?
          documents.data.map(item => <DocumentItem info={item}/>)
          : <div className={styles.noDocs}>You have no documents yet</div>
        }
      </Row>
    </div>
  );
};

export default Documents;

// todo doc date from back
const DocumentItem = ({info}) => (
  <Col4 className={styles.documentContainer}>
    <TouchableOpacity className={cn(templatesStyles.listItem, styles.item)}>
      <div className={templatesStyles.listItemIconContainer}>
        <Image height={25} src={clipboardBlueImagePath}/>
      </div>
      <div className={templatesStyles.listItemContentContainer}>
        <Col12 className={templatesStyles.date}>
          {moment().format('MM.DD.YYYY')}
        </Col12>
        <Col12 className={templatesStyles.listItemText}>
          {info.title}
        </Col12>
      </div>
    </TouchableOpacity>
  </Col4>
);
