import React from 'react';

// send data to server in these funcs also?

export const createDeleteItemFunc = (sourceFieldName, resolver, idFieldName) => {
  return (id) => resolver(({[sourceFieldName]: source, ...rest}) => ({
    [sourceFieldName]: Object.assign(source, {
      data: source.data.filter(item => item[idFieldName] !== id)
    }),
    ...rest
  }));
};

export const createEditItemFunc = (sourceFieldName, resolver, idFieldName) => {
  return (id, buffer) => resolver(({[sourceFieldName]: source, ...rest}) => ({
    [sourceFieldName]: Object.assign(source, {data: source.data.map(
      item => item[idFieldName] === id ? buffer : item
    )}),
    ...rest
  }));
};

// TODO ask :)
export const createAddItemFunc = (field, resolver, idFieldName) => {
  return (item) => resolver(({[field]: source, ...rest}) => ({
    [field]: Object.assign(source, {data: [
      ...source.data,
      Object.assign({...item}, {
        [idFieldName]: source.data[source.data.length-1][idFieldName] + 1
      }),
    ]}),
    ...rest
  }));
};

export class DataField {
  constructor(sourceFieldName, idFieldName, data=[], resolver=()=>{}) {
    this.data = data;
    this.add = createAddItemFunc(sourceFieldName, resolver, idFieldName);
    this.edit = createEditItemFunc(sourceFieldName, resolver, idFieldName);
    this.delete = createDeleteItemFunc(sourceFieldName, resolver, idFieldName);
  }
}

export class StateField {
  constructor(fieldName='', data={}, resolver=()=>{}, networkSetter=()=>{}) {
    this.data = data;
    this.set = async (data) => {
      await networkSetter();
      resolver(({[fieldName]: field, ...global}) => ({
        [fieldName]: {...field, data: JSON.parse(JSON.stringify(data))},
        ...global
      }));
    };
  }
}

export const contextShape = {
  services: new DataField('services', 'id'),
  partners: new DataField('partners', 'id'),
  competitors: new DataField('competitors', 'id'),
  market: new DataField('market', 'id'),
};

const DataContext = React.createContext(contextShape);

export default DataContext;
