import React, {useState, useContext} from "react";
import Wrapper from "../../../common/core/Wrapper";
import DashboardTabs from '../../../common/parts/DashboardTabs';
import Button from '../../../common/core/Button';
import ServicesTab from "./ServicesTab";
import CataloguesTab from './CataloguesTab';
import ModalService from './ModalService';
import ElectByIndex from "../../../common/core/ElectByIndex";
import Enum from "../../../../utils/enum";

const [SERVICE_MODAL] = Enum(1);

function Services() {
  const [modalIndex, setModalIndex] = useState(null);
  const closeModal = () => setModalIndex(null);

  const openEditModal = (service) => {
    setEditingValues(service);
    setModalIndex(SERVICE_MODAL);
  };

  const [editingValues, setEditingValues] = useState({});

  const modals = [
    <ModalService
      editingValues={editingValues}
      close={closeModal}
    />,
  ];

  // TODO refactor (mixed low and high level)
  const TABS = [{
    name: 'SERVICES',
    content: (
      <ServicesTab
        openEditModal={openEditModal}
      />
    ),
    controllers: [
      <Button
        title={'add new'}
        onClick={()=>setModalIndex(0)}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }, {
    name: 'CATALOGUES',
    content: <CataloguesTab/>,
    controllers: [
      <Button
        title={'upload .pdf'}
        height={27}
        width={135}
        fontSize={'0.9rem'}
      />
    ],
  }];

  const contents = TABS.map(item => (
    <Wrapper>{item.content}</Wrapper>
  ));

  const main = <DashboardTabs
    tabs={TABS}
    readyContents={contents}
    readyControllers
    startTabIndex={0}
  />;

  return (
    <ElectByIndex
      index={modalIndex}
      defaultComponent={main}
    >
      {modals}
    </ElectByIndex>
  );
}


export default Services;
