import moment from "moment";
import getSortedByDate from "./additional/getSortedByDate";
import {GET} from "../utils/network";

const getData = async (path) => {
  try {
    const res = await GET(path);
    // console.log(res);
    return res.body.data;
  } catch (e) {
    // TODO
    // console.error(e);
    return null;
  }
};

export const getAccountData = async () => {
  return await getData('/api/Account/getinfo');
};

export const getCompanyData = async () => {
  return (await getData('/api/Company/getlist'))[0];
};

export const getBusinessData = async () => {
  return (await getData('/api/Business/getlist'))[0];
};

export const getVariableFieldsData = async () => {
  return (await getData('/api/Template/variablefields'));
};

export const getTemplatesData = async () => {
  return await getData('/api/Template/getlist');
};

export const getTemplateById = async (id) => {
  return await getData(`/api/Template/get/${id}`)
};

export const getTemplateByKeywords = async (keywords) => {
  const searchParams = new URLSearchParams();
  searchParams.append('keywords', keywords);
  return await getData(`/api/Template/get?${searchParams.toString()}`);
};

