import Button from "../../../common/core/Button";
import React, {useContext, useEffect, useState} from "react";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import ElectByIndex from "../../../common/core/ElectByIndex";
import Wrapper from "../../../common/core/Wrapper";
import CustomersB2B from './CustomersB2B';
import CustomersB2P from './CustomersB2P';
import ModalB2B from './ModalB2B';
import ModalB2P from './ModalB2P';
import Enum from "../../../../utils/enum";
import Activities from "./Activities";
import {CustomerB2BDataContext, CustomerB2PDataContext} from "../../../../DATA_CONTROL/dataContexts";
import {isEmpty} from "../../../../utils/StringUtils";

const [ADD_NEW_B2B, ADD_NEW_B2P, ACTIVITY] = Enum(3);

const Customers = () => {
  const [modalIndex, setModalIndex] = useState(null);
  const customersB2B = useContext(CustomerB2BDataContext);
  const customersB2P = useContext(CustomerB2PDataContext);

  const submitCreateB2B = async (formState) => {
    await customersB2B.create(formState);
    closeModal();
  };

  const submitCreateB2P = async (formState) => {
    await customersB2P.create(formState);
    closeModal();
  };

  const submitEditB2B = async (formState) => {
    await customersB2B.update({...formState, id: editingB2BValues.id});
    closeModal();
  };

  const submitEditB2P = async (formState) => {
    await customersB2P.update({...formState, id: editingB2PValues.id});
    closeModal();
  };

  const [activityModal, setActivityModal] = useState(null);
  useEffect(() => {
    if (activityModal !== null) {
      setModalIndex(ACTIVITY);
    }
  }, [activityModal]);

  const createActivityModal = ({customerId}) => {
    setActivityModal(
      <Activities
        customerId={customerId}
        onClose={closeModal}
      />
    );
  };

  const openB2BAddNewModal = () => {
    setEditingB2BValues({});
    setModalIndex(ADD_NEW_B2B);
  };

  const openB2PAddNewModal = () => {
    setEditingB2PValues({});
    setModalIndex(ADD_NEW_B2P);
  };

  const [editingB2BValues, setEditingB2BValues] = useState({});
  const [editingB2PValues, setEditingB2PValues] = useState({});

  const openB2BEditModal = (customer) => {
    setEditingB2BValues(customer);
    setModalIndex(ADD_NEW_B2B);
  };

  const openB2PEditModal = (customer) => {
    setEditingB2PValues(customer);
    setModalIndex(ADD_NEW_B2P);
  };

  const closeModal = () => {
    setModalIndex(null);
  };

  const MODALS = [
    <ModalB2B
      onSubmit={isEmpty(editingB2BValues) ? submitCreateB2B : submitEditB2B}
      close={closeModal}
      editingValues={editingB2BValues}
    />,
    <ModalB2P
      onSubmit={isEmpty(editingB2PValues) ? submitCreateB2P : submitEditB2P}
      close={closeModal}
      editingValues={editingB2PValues}
    />,
    activityModal
  ];

  const TABS = [{
    name: 'CUSTOMERS B2B',
    content: (
      <CustomersB2B
        createActivityModal={createActivityModal}
        openEditModal={openB2BEditModal}
      />
    ),
    controllers: [
      <Button
        title={'add new'}
        onClick={openB2BAddNewModal}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }, {
    name: 'CUSTOMERS B2P',
    content: (
      <CustomersB2P
        createActivityModal={createActivityModal}
        openEditModal={openB2PEditModal}
      />
    ),
    controllers: [
      <Button
        title={'add new'}
        onClick={openB2PAddNewModal}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }];

  const contents = TABS.map(item => <Wrapper>{item.content}</Wrapper>);

  const main = <DashboardTabs
    tabs={TABS}
    readyContents={contents}
    startTabIndex={0}
  />;

  return (
    <ElectByIndex
      index={modalIndex}
      defaultComponent={main}
    >
      {MODALS}
    </ElectByIndex>
  );

};

export default Customers;
